import React, { Component } from "react";
import { Link } from "react-router-dom";

import { Navbar, Nav, NavDropdown } from "react-bootstrap";
import { HashLink as HLink } from "react-router-hash-link";

import { auth } from "./firebaseCloud";
import Auth from "../Route/Auth";

//important connect with Main Router Link
import { withRouter } from "react-router-dom";
import MissionModal from "../MissionVission/MissionModal";
import VisionModal from "../MissionVission/VisionModal";
import CoreValuesModal from "../MissionVission/CoreValuesModal";

class NavItem extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  logOut = () => {
    //fireabse authentication Signout
    this.props.handleLogout();
    this.props.history.push("/");
    auth.signOut().then(() => {
      Auth.signout();

      var lout = document.getElementById("logout");
      lout.classList.add("hide");
    });
  };

  render() {
    return (
      // Navigation Format
      <div>
        <Navbar
          id="nav"
          expand="lg"
          variant="light"
          className="navbar navbar-expand-xl navbar-dark bg-success "
        >
          <Navbar.Brand>
            <HLink to="/#nav" className="navbar-brand active">
              DFLCMCFI
            </HLink>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mr-auto">
              <NavDropdown title="School" id="exam">
                <HLink to="/Admission#nav" className="dropdown-item">
                  Admission
                </HLink>

                <HLink to="/SchoolEventsDisplay#nav" className="dropdown-item">
                  Events
                </HLink>

                <HLink
                  to="/SchoolAnnouncementsDisplay#nav"
                  className="dropdown-item"
                >
                  Announcements
                </HLink>
              </NavDropdown>

              <NavDropdown title="Programs" id="">
                <HLink to="/BsComSci#nav" className="dropdown-item">
                  Bs in Computer Science
                </HLink>
                <HLink to="/BsAccount#nav" className="dropdown-item">
                  Bs in Accountancy
                </HLink>
                <HLink to="/BsBa#nav" className="dropdown-item">
                  Bs in Business Administration
                </HLink>
                <HLink to="/BsBio#nav" className="dropdown-item">
                  Bachelor of Secondary Education Major in Biological Science
                </HLink>
                <HLink to="/BsEnglish#nav" className="dropdown-item">
                  Bachelor of Secondary Education Major in English
                </HLink>
                <HLink to="/BsFilipino#nav" className="dropdown-item">
                  Bachelor of Secondary Education Major in Filipino
                </HLink>
                <HLink to="/BsMath#nav" className="dropdown-item">
                  Bachelor of Secondary Education Major in Mathematics
                </HLink>
                <HLink to="/BsSs#nav" className="dropdown-item">
                  Bachelor of Secondary Education Major in Social Studies
                </HLink>
                <HLink to="/BsTle#nav" className="dropdown-item">
                  Bachelor of Secondary Education Major in Technology and
                  Livelihood Education
                </HLink>
                <HLink to="/BsMidWife#nav" className="dropdown-item">
                  Bachelor of Science in Midwifery
                </HLink>
                <HLink to="/BsEd#nav" className="dropdown-item">
                  Bachelor of Secondary Education
                </HLink>

                <NavDropdown.Divider />
                <HLink to="/HighDesc#nav" className="dropdown-item">
                  High School
                </HLink>
                <HLink to="/GradeDesc#nav" className="dropdown-item">
                  Grade School
                </HLink>
              </NavDropdown>

              <HLink to="/Library#nav" className="nav-link">
                Library
              </HLink>
              <NavDropdown title="Student">
                <HLink to="/HighSchoolExam#nav" className="dropdown-item">
                  Grade School - High School Exam Schedule
                </HLink>

                <HLink to="/CollegeExam#nav" className="dropdown-item">
                  College Exam Schedule
                </HLink>
                <NavDropdown.Divider />
                <HLink to="/StudentAccess#nav" className="dropdown-item">
                  Student Grade Checker
                </HLink>
                <NavDropdown.Divider />
                <HLink to="/GraduateTable#nav" className="dropdown-item">
                  Gradutation Candidates
                </HLink>
              </NavDropdown>

              <NavDropdown title="School Gallery">
                <HLink to="/gallery1" className="dropdown-item">
                  Library
                </HLink>
                <HLink to="/gallery2" className="dropdown-item">
                  Class Rooms
                </HLink>
                <HLink to="/gallery3" className="dropdown-item">
                  gallery3 sample
                </HLink>
                <HLink to="/gallery4" className="dropdown-item">
                  gallery4 sample
                </HLink>
              </NavDropdown>
              {/*==============================Test===========================================*/}
              <Link to="/test" className="nav-link">
                Test
              </Link>
              <Link to="/test1" className="nav-link">
                Test1
              </Link>
              {/*==============================Test===========================================*/}
            </Nav>
          </Navbar.Collapse>
          <form className="navbar-nav ml-auto">
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="mr-auto">
                <NavDropdown title="About">
                  <button
                    type="button"
                    className="dropdown-item"
                    data-toggle="modal"
                    data-target="#missionModal"
                  >
                    Mission
                  </button>
                  <button
                    type="button"
                    className="dropdown-item"
                    data-toggle="modal"
                    data-target="#visionModal"
                  >
                    Vision
                  </button>
                  <button
                    type="button"
                    className="dropdown-item"
                    data-toggle="modal"
                    data-target="#coreModal"
                  >
                    Core Values
                  </button>

                  <HLink to="/Hymn##nav" className="dropdown-item ">
                    DFLCMCFI Hymn
                  </HLink>

                  <NavDropdown.Divider />
                  <HLink to="/#history" className="dropdown-item">
                    History
                  </HLink>
                </NavDropdown>

                <NavDropdown title="Admin">
                  <HLink
                    id="adaccess"
                    to="/admin#nav"
                    className="dropdown-item "
                  >
                    Admin Access
                  </HLink>

                  <Link
                    id="adpage"
                    to="/AdminMainControl#nav"
                    className="dropdown-item hide"
                  >
                    Admin Page
                  </Link>
                </NavDropdown>

                <div className="nav-link">
                  Status: {this.props.loggedInStatus}
                </div>
                <button
                  id="logout"
                  className="btn nav-link active hide"
                  onClick={() => {
                    this.logOut();
                  }}
                >
                  Log-out
                </button>
              </Nav>
            </Navbar.Collapse>
          </form>
        </Navbar>

        {/* Modal Mission*/}
        <MissionModal />
        <VisionModal />
        <CoreValuesModal />
      </div>
    );
  }
}
//important use with Router in here
export default withRouter(NavItem);
