import React, { Component } from "react";

import { connect } from "react-redux";
import { getBsSs } from "../Redux/actionFunction";

import { MDBDataTable } from "mdbreact";
import _ from "lodash";

import Pic from "../Img/BsSs/Image.png";
import Pic1 from "../Img/BsSs/Image1.png";
import Pic2 from "../Img/BsSs/Image2.png";
import Pic3 from "../Img/BsSs/Image3.png";
import Pic4 from "../Img/BsSs/Image4.png";

class BsSs extends Component {
  componentDidMount() {
    this.props.getBsSs();
  }

  /////////MDBTable

  table = () => {
    const data = {
      columns: [
        {
          label: "Subject",
          field: "subject",
          sort: "asc",
          width: 150
        },
        {
          label: "Unit",
          field: "unit",
          sort: "asc",
          width: 270
        },
        {
          label: "Pre-Requisites",
          field: "prerequisites",
          sort: "asc",
          width: 200
        },
        {
          label: "Semester",
          field: "semester",
          sort: "asc",
          width: 200
        },
        {
          label: "Year",
          field: "year",
          sort: "asc",
          width: 200
        },
        {
          label: "Button",
          field: "handle",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        subject: record.subject,
        unit: record.unit,
        prerequisites: record.prerequisites,
        semester: record.sem,
        year: record.year
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };
  render() {
    return (
      <div className="container-fluid bgC">
        <header className="bsSsHeader text-center text-white" id="socialheader">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0" style={{ color: "red" }}>
                Bachelor of Secondary Education Major in Social Studies
              </h1>
              <p style={{ color: "black" }}>
                The Bachelor in Secondary Education Major in Social Studies
                (BSEd  Social Studies) is a four-year degree program in the
                Philippines that will introduce you to governments and politics,
                economic planning and strategy, and selected laws related to
                them.
              </p>
              <a
                href="#main"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Learn More
              </a>
              <a
                href="#curriculum"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Curriculum
              </a>
            </div>
          </div>
          <div className="bg-circle-1 bg-circle"></div>
          <div className="bg-circle-2 bg-circle"></div>
          <div className="bg-circle-3 bg-circle"></div>
          <div className="bg-circle-4 bg-circle"></div>
        </header>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={Pic} alt="" />
                  <img className="img-fluid rounded-circle" src={Pic1} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">About the Program</h2>
                  <p>
                    The aim of social studies is the promotion of civic
                    competencethe knowledge, intellectual processes, and
                    democratic dispositions required of students to be active
                    and engaged participants in public life. By making civic
                    competence a central aim, NCSS emphasizes the importance of
                    educating students who are committed to the ideas and values
                    of democracy. Civic competence rests on this commitment to
                    democratic values, and requires that citizens have the
                    ability to use their knowledge about their community,
                    nation, and world; to apply inquiry processes; and to employ
                    skills of data collection and analysis, collaboration,
                    decision-making, and problem-solving. Young people who are
                    knowledgeable, skillful, and committed to democracy are
                    necessary to sustaining and improving our democratic way of
                    life, and participating as members of a global community.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 order-lg-2">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={Pic3} alt="" />
                  <img className="img-fluid rounded-circle" src={Pic4} alt="" />
                </div>
              </div>
              <div className="col-lg-6 order-lg-1">
                <div className="p-5">
                  <h2 className="display-4">Compentencies</h2>
                  <ul>
                    <li>
                      Manage a strategic business unit for economic
                      sustainability.
                    </li>
                    <li>Innovate business ideas based on emerging industry.</li>
                    <li>Prepare operational plans.</li>
                    <li>
                      Analyze the business environment for strategic direction.
                    </li>
                    <li>Exercise high personal moral and ethical standards.</li>
                    <li>
                      Demonstrate corporate citizenship and social
                      responsibility.
                    </li>
                    <li>Plan and implement business-related activities.</li>
                    <li>
                      Work effectively with other stakeholders and manage
                      conflict in the workplace.
                    </li>
                    <li>
                      Apply information and communication technology (ICT)
                      skills as required by the business environment.
                    </li>
                    <li>
                      Express oneself clearly and communicate effectively with
                      stakeholders
                    </li>
                    <li>
                      Select the proper decision-making tools to critically,
                      analytically and creatively solve problems, and drive
                      results.
                    </li>
                    <li>
                      Apply the basic concepts that underlie each of the
                      functional areas of business (marketing, finance, human
                      resources management, production and operations
                      management, information technology, and strategic
                      management) and employ these concepts in various business
                      situations.
                    </li>
                    <li>
                      Perform the basic functions of management such as
                      planning, organizing, leading, and controlling.
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={Pic2} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Career Opportunities</h2>
                  <ul>
                    <li>Aging Studies</li>
                    <li>Economics</li>
                    <li>Environmental Studies</li>
                    <li>Geography</li>
                    <li>International Affairs</li>
                    <li>Interdisciplinary Social Science</li>
                    <li>Political Science</li>
                    <li>Public Administration</li>
                    <li>Sociology</li>
                    <li>Urban and Regional Planning</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="curriculum">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-5">
                  <div className="table-wrapper-scroll-y my-custom-scrollbar"></div>
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Current Program Curriculum</h2>
                  <p>
                    In education, a curriculum is broadly defined as the
                    totality of student experiences that occur in the
                    educational process. The term often refers specifically to a
                    planned sequence of instruction, or to a view of the
                    student's experiences in terms of the educator's or school's
                    instructional goals.
                  </p>
                  <p>Click the column name to sort the records</p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section className="text-center">{this.table()}</section>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getBsSs
})(BsSs);
