import React, { Component } from "react";
import Sound from "../Music/IBCMI.mp3"; //still searching for the real song, this is just a sample.. PEACE!!!!

import { Media, Player, controls, utils } from "react-media-player";
const {
  PlayPause,
  CurrentTime,

  SeekBar,
  Duration,
  MuteUnmute,
  Volume,
  Fullscreen
} = controls;
const { keyboardControls } = utils;

export default class Hymn extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  render() {
    return (
      <div className="container-fluid" id="headerhymn">
        <Media>
          {mediaProps => (
            <div
              className="media"
              onKeyDown={keyboardControls.bind(null, mediaProps)}
            >
              <Player src={Sound} className="media-player" />
              <div className="media-controls">
                <section id="main">
                  <div className="container">
                    <div className="row align-items-center">
                      <div className="col-lg-6 order-lg-2">
                        <div className="p-5">
                          <PlayPause />
                          <br />
                          <CurrentTime />
                          <br />

                          <SeekBar />
                          <br />
                          <Duration />
                          <br />
                          <MuteUnmute />
                          <br />
                          <Volume />
                          <br />
                          <Fullscreen />
                        </div>
                      </div>

                      <div className="p-5">
                        <h2 className="display-4">Awit ng IBCMI</h2>
                        <h5>(Titik ni Dr. Francisco L. Calingasan)</h5>
                        <p>
                          Aklat at liwanag ang siya naming sagisag <br />
                          sa pag tuklas ng karunungang busilak <br />
                          Ito'y gabay sa pag-aaral na masikap <br />
                          Upang magtagumpay tayong lahat <br />
                          <br />
                          Mga guro ang pawang tagapatnubay
                          <br />
                          karununga'y sa inyo utang <br />
                          Kung kaya't ang layunin ng IBCMI <br />
                          Paunlarin at pangalagaan <br />
                          <br />
                          Kaya't tayong lahat ngayoy magsama-sama
                          <br />
                          Sa IBCMI na siyang gabay.
                        </p>
                      </div>
                    </div>
                  </div>
                </section>
              </div>
              <div>{/* TODO */}</div>
            </div>
          )}
        </Media>
      </div>
    );
  }
}
