import React, { Component } from "react";

export default class CoreValuesModal extends Component {
  render() {
    return (
      <div>
        <div
          className="modal fade"
          id="coreModal"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="exampleModalCenterTitle"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-dialog-centered" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLongTitle">
                  Core Values
                </h5>

                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-label="Close"
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <div>
                  <ul>
                    <li>Maka-Diyos</li>
                    <p>
                      Expresses one's spiritual beliefs while respecting the
                      spiritual beliefs of others. Show adherence to ethical
                      principles by upholding truth.
                    </p>
                    <li>Makatao</li>
                    <p>
                      Is sensitive to individual social, and cultural
                      differences. Demonstrates contributions towards
                      solidarity.
                    </p>
                    <li>Maka-Kalikasan</li>
                    <p>
                      Cares for the environment and utilize resources
                      wisely,judiciously and economically.
                    </p>
                    <li>Makabansa</li>
                    <p>
                      Demonstrates pride in being a Filipino;exercise the rights
                      and responsibilities of a Filipino citizen. Demonstrates a
                      appropriate behavior in carrying out activities in the
                      school community and country.
                    </p>
                  </ul>
                </div>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-secondary"
                  data-dismiss="modal"
                >
                  Close
                </button>
                {/* <button type="button" className="btn btn-primary">
            Save changes
          </button> */}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
