import React, { Component } from "react";
import { connect } from "react-redux";
import { getAnnounce } from "../Redux/actionFunction";

import _ from "lodash";

class SchoolAnnouncementsDisplay extends Component {
  componentDidMount() {
    ///@@@@@@@@@@@@@@@@@funtion from redux
    this.props.getAnnounce();
  }

  render() {
    return (
      <div className="container-fluid ">
        <header className="masthead text-center text-white" id="sAnnounce">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0">School Announcements</h1>
              <p>Attention!!!</p>
              <a
                href="#main"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Learn More
              </a>
            </div>
          </div>
          <div className="bg-circle-1 bg-circle"></div>
          <div className="bg-circle-2 bg-circle"></div>
          <div className="bg-circle-3 bg-circle"></div>
          <div className="bg-circle-4 bg-circle"></div>
        </header>
        {_.map(this.props.records, (record, key) => (
          <section id="main" key={key}>
            <div className="container">
              <div className="row align-items-center">
                <div className="col-lg-6 order-lg-2">
                  <div className="p-5">
                    <img
                      className="img-fluid rounded-circle"
                      src={record.pictureUrl}
                      alt=""
                    />
                  </div>
                </div>
                <div className="col-lg-6 order-lg-1">
                  <div className="p-5">
                    <h2 className="display-4">{record.title}</h2>
                    <p>{record.date}</p>
                    <p>{record.description}</p>
                  </div>
                </div>
              </div>
            </div>
          </section>
        ))}

        {/* <div className="container-fluid" id="eventtb">
          <div className="m-3 d-flex justify-content-center">
            <div className="form-group">
              <h3 className="m-3 d-flex justify-content-center">
                Announcements
              </h3>
              <Table
                className="table  table-borderless thead-dark"
                hover
                size="xl"
                variant="light"
              >
                <thead>
                  <tr className="table ">
                    <th>Image</th>
                    <th style={{ width: "150px" }}>Title</th>
                    <th style={{ width: "100px" }}>Date</th>
                    <th>Description</th>
                  </tr>
                </thead>
                <tbody className="success">
                  {_.map(this.props.records, (record, key) => (
                    <tr key={key}>
                      <td>
                        <img
                          className="imgTable"
                          alt=""
                          src={record.pictureUrl}
                        />
                      </td>

                      <td>{record.title}</td>
                      <td>{record.date}</td>
                      <td>{record.description}</td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            </div>
          </div>
        </div> */}
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}
export default connect(mapStateToProps, {
  getAnnounce
})(SchoolAnnouncementsDisplay);
