import React, { Component } from "react";
import {
  HashRouter,
  Switch,
  Route,
  Redirect
} from "react-router-dom";

import NavItem from "../Components/NavItem";
import HomeHtml from "../Components/HomeHtml";
import SchoolAnnouncementsDisplay from "../Components/SchoolAnnouncementsDisplay";
import SchoolEventsDisplay from "../Components/SchoolEventsDisplay";
import Auth from "../Route/Auth";
import LoginPage from "../Components/LoginPage";
import Admin from "../Admin/Admin";
import AdminMainPage from "../Admin/AdminMainPage";
import Admission from "../Components/Admission";
import BsComSci from "../Programs/BsComSci";
import BsAccount from "../Programs/BsAccount";
import BsBa from "../Programs/BsBa";
import Error404 from "../Components/Error404";
import BsBio from "../Programs/BsBio";
import BsEnglish from "../Programs/BsEnglish";
import BsFilipino from "../Programs/BsFilipino";
import BsMath from "../Programs/BsMath";
import BsSs from "../Programs/BsSs";
import BsTle from "../Programs/Bstle";
import Mission from "../MissionVission/Mission";
import Library from "../Library/Library";
import College from "../Exam/College";
import HighSchool from "../Exam/HighSchool";
import BsMidwifery from "../Programs/BsMidwifery";
import Footer from "../Components/Footer";
import gallery1 from "../DFLCMCFI/gallery1";
import gallery2 from "../DFLCMCFI/gallery2";
import Hymn from "../Hymn/Hymn";
import StudentAccess from "../StudentGrades/StudentAccess";
import HigschoolDesc from "../HighSchool/HigschoolDesc";
import GradeSchool from "../HighSchool/GradeSchool";
import Graduates from "../AdminAccess/Graduates";
import GraduatesTable from "../Components/GraduatesTable";
import BsEdControl from "../Programs/BsEdControl";
import BsEd from "../Programs/BsEd";

//Private route
const PrivateRoute = ({ component: Component, ...rest }) => (
  <Route
    {...rest}
    render={props =>
      Auth.getAuth() ? (
        <Component {...props} />
      ) : (
        <Redirect
          to={{
            pathname: "/404"
          }}
        />
      )
    }
  />
);

export default class RouterMain extends Component {
  render() {
    return (
      <div className="">
       <HashRouter basename="/">
          <div>
            <div className="container-fluid mainContainerWidth">
              <div className="container-fluid">
                <div className="container-fluid">
                  <div className="container-fluid">
                    {/* Navigation Bar */}
                    <NavItem
                      handleLogout={this.props.handleLogout}
                      loggedInStatus={this.props.loggedInStatus}
                    />
                    <Switch>
                      {/*======TEST==============Put your component here===========================================*/}
                      <Route path="/test" component={BsEd}></Route>
                      {/*==============================Test===========================================*/}
                      {/*======TEST==============Put your component here===========================================*/}
                      <Route path="/test1" component={BsEdControl}></Route>
                      {/*==============================Test===========================================*/}
                      {/* Error Page */}
                      <Route path="/404" exact component={Error404}></Route>
                      <Route exact path="/" exact component={HomeHtml}></Route>
                      <Route path="/Admission" component={Admission}></Route>
                      <Route
                        path="/SchoolEventsDisplay"
                        component={SchoolEventsDisplay}
                      ></Route>
                      <Route
                        path="/SchoolAnnouncementsDisplay"
                        component={SchoolAnnouncementsDisplay}
                      ></Route>
                      {/* DropDown Exam Schedule*/}
                      <Route path="/CollegeExam" component={College} />
                      <Route path="/HighSchoolExam" component={HighSchool} />
                      {/* DropDown Programs*/}
                      <Route path="/BsComSci" component={BsComSci}></Route>
                      <Route path="/BsAccount" component={BsAccount}></Route>
                      <Route path="/BsBa" component={BsBa}></Route>
                      <Route path="/BsBio" component={BsBio}></Route>
                      <Route path="/BsEnglish" component={BsEnglish}></Route>
                      <Route path="/BsFilipino" component={BsFilipino}></Route>
                      <Route path="/BsMath" component={BsMath}></Route>
                      <Route path="/BsSs" component={BsSs}></Route>
                      <Route path="/BsTle" component={BsTle}></Route>
                      <Route path="/BsMidWife" component={BsMidwifery}></Route>
                      <Route path="/BsEd" component={BsEd}></Route>

                      <Route path="/HighDesc" component={HigschoolDesc}></Route>
                      <Route path="/GradeDesc" component={GradeSchool}></Route>
                      <Route
                        path="/GraduateTable"
                        component={GraduatesTable}
                      ></Route>

                      <Route path="/Library" component={Library}></Route>
                      {/* Gallery */}
                      <Route path="/gallery1" component={gallery1}></Route>
                      <Route path="/gallery2" component={gallery2}></Route>

                      {/* University Hymn */}
                      <Route path="/Hymn" component={Hymn}></Route>

                      {/* Student Access */}
                      <Route
                        path="/StudentAccess"
                        component={StudentAccess}
                      ></Route>

                      {/* Right Side Nav */}
                      <Route path="/MVC" component={Mission}></Route>
                      <Route
                        path="/Authentication"
                        component={LoginPage}
                      ></Route>
                      {/* Private Page =================================================== */}
                      <PrivateRoute
                        exact
                        path="/AdminMainControl"
                        component={AdminMainPage}
                        render={props => <AdminMainPage {...props} />}
                      />
                      {/* Important */}
                      <Route
                        exact
                        path="/admin"
                        render={props => (
                          <Admin
                            {...props}
                            loggedInStatus={this.props.loggedInStatus}
                            handleLogin={this.props.handleLogin}
                            handleLogout={this.props.handleLogout}
                          />
                        )}
                      />
                    </Switch>
                    <Footer />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </HashRouter>
      </div>
    );
  }
}
