import React, { Component } from "react";
import bscs from "../Img/BsCs/bscs1.jpg";

import Pic1 from "../Img/BsEd/Pic1.jpg";
import Pic2 from "../Img/BsEd/Pic2.png";
import Pic3 from "../Img/BsEd/Pic3.jpg";

import { connect } from "react-redux";
import { getBSED } from "../Redux/actionFunction";

import { MDBDataTable } from "mdbreact";
import _ from "lodash";

class BsEd extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  componentDidMount() {
    this.props.getBSED();

    console.log("State", this.state.data);
  }

  table = () => {
    const data = {
      columns: [
        {
          label: "Subject",
          field: "subject",
          sort: "asc",
          width: 150
        },
        {
          label: "Unit",
          field: "unit",
          sort: "asc",
          width: 270
        },
        {
          label: "Pre-Requisites",
          field: "prerequisites",
          sort: "asc",
          width: 200
        },
        {
          label: "Semester",
          field: "semester",
          sort: "asc",
          width: 200
        },
        {
          label: "Year",
          field: "year",
          sort: "asc",
          width: 200
        }
      ],
      rows: [
        {
          name: "Tiger Nixon",
          position: "System Architect",
          office: "Edinburgh",
          age: "61",
          date: "2011/04/25",
          salary: "$320"
        }
      ]
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        subject: record.subject,
        unit: record.unit,
        prerequisites: record.prerequisites,
        semester: record.sem,
        year: record.year
      };

      return data.rows.push(row);
    });

    return (
      <div>
        <MDBDataTable bordered striped hover data={data} />
      </div>
    );
  };

  render() {
    return (
      <div className="container-fluid bgC">
        <header className="bsEd text-center text-white" id="sciheader">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0" style={{ color: "red" }}>
                Bachelor of Secondary Education (BSED)
              </h1>
              <p style={{ color: "black" }}>
                The most important day of a persons education is the first day
                of school, not Graduation Day.
              </p>
              <a
                href="#bscs"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Learn More
              </a>
              <a
                href="#curriculum"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Curriculum
              </a>
            </div>
          </div>
        </header>
        <section id="bscs">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 order-lg-2">
                <div className="p-5">
                  <img className="img-fluid rounded-circle" src={Pic1} alt="" />
                </div>
              </div>
              <div className="col-lg-6 order-lg-1">
                <div className="p-5">
                  <h2 className="display-4">
                    About Bachelor of Secondary Education
                  </h2>
                  <p>
                    Bachelor of Secondary Education (BSED) is a four year degree
                    program designed to prepare students for becoming high
                    school teachers. The program combines both theory and
                    practice in order to teach students the necessary knowledge
                    and skills a high school teacher needs.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section>
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-5">
                  <img className="img-fluid rounded-circle" src={Pic2} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Get Enrolled</h2>
                  <p>
                    The course provides students with a general training in
                    education and developmental psychology, sociology and
                    linguistics applied to education, especially to students
                    with special learning needs.
                  </p>

                  <ul>
                    <li>
                      Philosophy, foundations, and principles of education
                    </li>
                    <li>Curricular Content Development</li>
                    <li>Development of Instructional Materials</li>
                    <li>Teaching Strategies</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section className="container-fluid">
          <div className="container-fluid">
            <div className="m-3 d-flex justify-content-center">
              <div className="form-group row align-items-center text-center">
                <ul>
                  <p>Competencies</p>
                  <p>
                    1. The Education Department aims to produce graduates who
                    demonstrate the following core competencies
                  </p>

                  <li>
                    Excellent character in teaching and in dealing with learners
                    with special needs.
                  </li>
                  <li>
                    Demonstrate understanding of learners with special needs and
                    methodologies of educating them.
                  </li>
                  <li>
                    Demonstrate compassion and concern for learners with special
                    needs.
                  </li>
                  <li>
                    Possess interpersonal skills and collaborative attitude in
                    dealing with parents of learners with special needs as well
                    as with the school personnel and people in the school
                    community.
                  </li>

                  <p>
                    Professional competence and research orientation in the
                    practice of the profession considering the international
                    standards.
                  </p>
                  <li>
                    Possess open-mindedness to and interest in innovations in
                    education based on research and employs them as appropriate;
                    initiates innovation to improve instruction.
                  </li>
                  <li>
                    Demonstrate flexibility in the use of teaching strategies
                    and approaches, particularly Outcomes Based Education, Brain
                    Based Learning, for learners with special needs given
                    diverse contexts.
                  </li>
                  <li>
                    Competently plan to make learning meaningful and purposeful
                    for learners with special needs.
                  </li>
                  <li>
                    Uphold ethical standards in the exercise of the profession.
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </section>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 order-lg-2">
                <div className="p-5">
                  <img className="img-fluid rounded-circle" src={Pic3} alt="" />
                </div>
              </div>
              <div className="col-lg-6 order-lg-1">
                <div className="p-5">
                  <h2 className="display-4">Career Opportunities</h2>
                  <ul>
                    <li>Community education officer</li>
                    <li>Early years teacher</li>
                    <li>Education administrator</li>
                    <li>Education consultant</li>
                    <li>Further education teacher</li>
                    <li>Learning mentor</li>
                    <li>Primary school teacher</li>
                    <li>Secondary school teacher</li>
                    <li>Special educational needs coordinator (SENCO)</li>
                    <li>Special educational needs teacher</li>
                    <li>Teaching assistant</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="curriculum">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-5">
                  <div className="table-wrapper-scroll-y my-custom-scrollbar"></div>
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Current Program Curriculum</h2>
                  <p>
                    In education, a curriculum is broadly defined as the
                    totality of student experiences that occur in the
                    educational process. The term often refers specifically to a
                    planned sequence of instruction, or to a view of the
                    student's experiences in terms of the educator's or school's
                    instructional goals.
                  </p>
                  <p>Click the column name to sort the records</p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section className="text-center">{this.table()}</section>
      </div>
    );
  }
}

//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getBSED
})(BsEd);
