import React, { Component } from "react";
import { connect } from "react-redux";
import { getGrad, deleteGrad } from "../Redux/actionFunction";
import { dbGrad } from "../Components/firebaseCloud";
import { MDBDataTable, MDBBtn } from "mdbreact";
import _ from "lodash";

class Graduates extends Component {
  constructor(props) {
    super(props);

    this.state = {
      name: "",
      course: "",
      yearlvl: "",
      remarks: ""
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.removeDisable = this.removeDisable.bind(this);
  }
  componentDidMount() {
    this.props.getGrad();
  }
  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  handleSubmit(e) {
    e.preventDefault();

    dbGrad.push({
      name: this.state.name,
      course: this.state.course,
      remarks: this.state.remarks,
      yearlvl: this.state.yearlvl
    });

    //Reset field for inputs
    this.setState({
      name: "",
      course: "",
      remarks: "",
      yearlvl: ""
    });
    this.yearlvl = this.state.yearlvl;
    alert("Record Updated");
  }

  /////////MDBTable

  table = () => {
    const data = {
      columns: [
        {
          label: "Full name",
          field: "name",
          sort: "asc",
          width: 150
        },
        {
          label: "Year level",
          field: "yearlvl",
          sort: "asc",
          width: 270
        },
        {
          label: "Course",
          field: "course",
          sort: "asc",
          width: 200
        },
        {
          label: "Remarks",
          field: "remarks",
          sort: "asc",
          width: 200
        },
        {
          label: "Button",
          field: "handle",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        name: record.name,
        yearlvl: record.yearlvl,
        course: record.course,
        remarks: record.remarks,

        handle: (
          <MDBBtn
            className="btn btn-danger"
            onClick={() => this.props.deleteGrad(key)}
            size="sm"
          >
            Delete
          </MDBBtn>
        )
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };

  removeDisable = () => {
    const a = document.getElementById("courseDisable");
    return (a.disabled = false);
  };

  render() {
    return (
      <div>
        <header className="masthead text-center text-white">
          <div className="masthead-content ">
            <div className="container">
              <h1 className="masthead-heading mb-0">
                Candidates for Graduation
              </h1>
              <p>Be bold, be courageous, be your best.</p>
            </div>
          </div>
        </header>
        <div className="container-fluid">
          <section id="curriculum">
            <div className="container">
              <div className="row align-items-center">
                <div className="col-lg-6">
                  <div className="p-5">
                    <div className="table-wrapper-scroll-y my-custom-scrollbar"></div>
                  </div>
                </div>
                <div className="col-lg-5">
                  <div className="p-5">
                    <h2 className="display-5">Graduation Candidates</h2>
                    <form onSubmit={this.handleSubmit}>
                      <div>
                        <input
                          onChange={this.handleChange}
                          value={this.state.name}
                          type="text"
                          name="name"
                          className="form-control no-border"
                          placeholder="Full name"
                          required
                        />
                        <select
                          id="yearlvl"
                          className="form-control no-border"
                          name="yearlvl"
                          onChange={this.handleChange}
                          required
                          defaultValue={this.state.yearlvl}
                          ref={ref => (this.yearlvl = ref)}
                        >
                          <option value={this.state.yearlvl} disabled>
                            Please select year level
                          </option>

                          <option
                            name="yearlvl"
                            value="High School"
                            onChange={this.handleChange}
                          >
                            High School
                          </option>
                          <option
                            name="yearlvl"
                            value="College"
                            onChange={this.handleChange}
                          >
                            College
                          </option>
                        </select>

                        <input
                          id="courseDisable"
                          onChange={this.handleChange}
                          value={this.state.course}
                          type="text"
                          name="course"
                          className="form-control no-border"
                          placeholder="Course"
                          disabled
                        />

                        <input
                          onChange={this.handleChange}
                          value={this.state.remarks}
                          type="text"
                          name="remarks"
                          className="form-control no-border"
                          placeholder="Remarks"
                        />

                        <button
                          type="submit"
                          className="btn btn-secondary d-flex col-sm-12"
                        >
                          Save
                        </button>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>

        <section className="text-center">{this.table()}</section>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getGrad,
  deleteGrad
})(Graduates);
