import React, { Component } from "react";
import { storage, dbCourse } from "../Components/firebaseCloud";
import _ from "lodash";
import { Table } from "react-bootstrap";
//Connect redux
import { connect } from "react-redux";
import { getCourse, deleteCourse } from "../Redux/actionFunction";

class Course extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedFile: null,
      course: "",
      date: "",
      description: "",
      pictureUrl: "",
      records: {},
      url: ""
    };
  }
  //Functions
  componentDidMount() {
    this.props.getCourse();
  }

  onChange(e) {
    this.setState({
      [e.target.name]: e.target.value
    });
  }
  onChangePic(e) {
    this.setState({
      selectedFile: e.target.files[0]
    });

    console.log(this.state.selectedFile);
  }
  //Save the item on Firebase Storage
  fileUpload = event => {
    event.preventDefault();
    const image = this.state.selectedFile;
    const uploadTask = storage.ref(`images/${image.name}`).put(image);
    uploadTask.on(
      "state_changed",
      snapshot => {
        //Progress
      },
      error => {
        //Error
        alert(error);
      },
      () => {
        //Complete
        storage
          .ref("images")
          .child(image.name)
          .getDownloadURL()
          .then(url => {
            console.log(url);
            this.setState({
              url
            });
            const records = {
              course: this.state.course,
              date: this.state.date,
              description: this.state.description,
              pictureUrl: url
            };
            //this reset the input field ==========
            this.setState({
              course: "",
              description: ""
            });
            //reset the input file field via reference
            this.file.value = "";

            dbCourse.push(records);
          });
      }
    );
  };

  renderNotes() {
    //parameters(object,(obj,key))

    return _.map(this.props.records, (record, key) => {
      return (
        //Table view
        <Table size="xl" key={key}>
          <thead>
            <tr>
              <th>Image</th>
              <th style={{ width: "150px" }}>Program</th>
              <th>Description</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>
                <img className="imgTable" alt="" src={record.pictureUrl} />
              </td>
              <td>{record.course}</td>
              <td>{record.description}</td>
              <td>
                <button
                  style={{ width: "80px" }}
                  className="btn btn-danger col-sm-12"
                  onClick={() => this.props.deleteCourse(key)}
                >
                  Delete
                </button>
              </td>
            </tr>
          </tbody>
        </Table>
      );
    });
  }

  render() {
    return (
      <div className="container=fluid">
        <h1 className="m-3 d-flex justify-content-center">Create Program</h1>

        <div className="m-3 d-flex justify-content-center">
          <div className="form-group">
            <form onSubmit={this.fileUpload.bind(this)}>
              <input
                type="text"
                name="course"
                className="form-control no-border"
                required
                value={this.state.course}
                onChange={e => {
                  this.onChange(e);
                }}
                style={{ width: "873px" }}
                placeholder="Program"
              />
              <br />
              <textarea
                type="text"
                name="description"
                className="form-control no-border"
                required
                value={this.state.description}
                onChange={e => {
                  this.onChange(e);
                }}
                style={{ width: "873px", height: "250px" }}
                placeholder="Description"
              />{" "}
              <br />
              Insert Image here:
              <input
                type="file"
                name="file"
                className="form-control no-border"
                required
                onChange={e => {
                  this.onChangePic(e);
                }}
                style={{ width: "250px" }}
                accept="image/x-png,image/gif,image/jpeg"
                //reference for reset field
                ref={ref => (this.file = ref)}
              />
              <br />
              <button
                className="btn btn-success col-sm-12"
                type="submit"
                value="sumbit"
              >
                Upload
              </button>
              <hr />
            </form>
          </div>
        </div>
        {this.renderNotes()}
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getCourse,
  deleteCourse
})(Course);
