import React, { Component } from "react";
import LoginPage from "../Components/LoginPage";

import Auth from "../Route/Auth";
import { auth } from "../Components/firebaseCloud";

class Admin extends Component {
  constructor(props) {
    super(props);

    this.state = {};
    this.handleSuccessfulAuth = this.handleSuccessfulAuth.bind(this);
    this.handleSuccessfulLogout = this.handleSuccessfulLogout.bind(this);
  }
  //Login Function Inherited from App to Routermain
  handleSuccessfulAuth(data) {
    this.props.history.push("/AdminMainControl");
    this.props.handleLogin(data);
  }
  handleSuccessfulLogout(data) {
    this.props.handleLogout(data);
    auth.signOut().then(() => {
      Auth.signout();
    });
  }

  render() {
    return (
      <div>
        <div>
          <LoginPage
            handleSuccessfulAuth={this.handleSuccessfulAuth}
            handleLogout={this.handleSuccessfulLogout}
            Status={this.props.loggedInStatus}
          />
          {/* <button
            onClick={() => {
              this.handleSuccessfulLogout();
            }}
          >
            Logout
          </button> */}
        </div>
      </div>
    );
  }
}
export default Admin;
