import React, { Component } from "react";
import "../css/one-page-wonder.min.css";
import SchoolOffice from "../Img/SchoolOffice.jpg";
import adm from "../Img/adm.jpg";

export default class Admission extends Component {
  render() {
    return (
      <div className="container-fluid bgC">
        <header className="masthead text-center text-white" id="admission">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0">Admissions</h1>
              <p>
                We warmly welcome junior high school completers, senior high
                school graduates, college transferees, second coursers, foreign
                applicants, and graduate program applicants to our campuses.
              </p>
              <a
                href="#main"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Learn More
              </a>
            </div>
          </div>
          <div className="bg-circle-1 bg-circle"></div>
          <div className="bg-circle-2 bg-circle"></div>
          <div className="bg-circle-3 bg-circle"></div>
          <div className="bg-circle-4 bg-circle"></div>
        </header>

        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 order-lg-2">
                <div className="p-5">
                  <img
                    className="img-fluid rounded-circle"
                    src={SchoolOffice}
                    alt=""
                  />
                </div>
              </div>
              <div className="col-lg-6 order-lg-1">
                <div className="p-5">
                  <h2 className="display-4">Enrollment Procedures</h2>
                  <p>
                    All campuses take pride in their staff who are able to
                    provide personalized assistance, fast, and efficient
                    admissions procedures on-campus and even online. In general
                    the following procedures shall be observed:
                  </p>
                  <ul>
                    <li>
                      Fill out the Student Admission Form at the Admission
                      Office
                    </li>
                    <li>
                      Submit the accomplished form together with the required
                      documents to the Admissions Officer for validation. An
                      Admission Requirements Checklist will be issued to the
                      student applicant to keep track of their documents.
                    </li>
                    <li>
                      The admission status will be sent to the applicants when
                      it is made available. Upon acceptance, proceed with the
                      rest of the enrollment process.
                    </li>
                    <li>
                      Consult the Admissions Officer about your desired
                      program/s to enroll in and the registration schedule.
                    </li>
                    <li>Pay the corresponding fees to the Cashier.</li>
                    <li>
                      Obtain a copy of the Registration and Assessment Form
                      (RAF) with the official stamp by presenting the official
                      receipt to the Registrar's Office.
                    </li>
                    <li>
                      Present your copy of the Registration and Assessment Form
                      (RAF) on the first day of classes for identification.
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>

        <section>
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-5">
                  <img className="img-fluid rounded-circle" src={adm} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Requirements</h2>
                  <p>
                    The following requirements must be submitted to the
                    Registrar's Office before admission to any academic program:
                  </p>
                  <p>College</p>
                  <ul>
                    <li>
                      Original Form 138 / SF9-JHS (Learner's Progress Report
                      Card)
                    </li>
                    <li>
                      Original Form 137 / SF10-JHS (Learner's Permanent Academic
                      Record)
                    </li>
                    <li>PSA issued Birth Certificate</li>
                  </ul>
                  <p>Senior High School</p>
                  <ul>
                    <li>
                      Original Form 138 / SF9-JHS (Learner's Progress Report
                      Card)
                    </li>
                    <li>
                      Original Form 137 / SF10-JHS (Learner's Permanent Academic
                      Record)
                    </li>
                    <li>PSA issued Birth Certificate</li>
                  </ul>
                  <p>College Transferees</p>
                  <ul>
                    <li>Certificate of Transfer (Honorable Dismissal)</li>
                    <li>Official Transcript of Records</li>
                    <li>PSA issued Birth Certificate</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
