import React, { Component } from "react";
import bsba from "../Img/BsBa/bsba.jpg";
import bsba1 from "../Img/BsBa/bsba1.jpg";
import bsba2 from "../Img/BsBa/bsba2.jpg";

import { connect } from "react-redux";
import { getBsBa } from "../Redux/actionFunction";
import { MDBDataTable } from "mdbreact";
import _ from "lodash";

class BsBa extends Component {
  componentDidMount() {
    this.props.getBsBa();
  }

  table = () => {
    const data = {
      columns: [
        {
          label: "Subject",
          field: "subject",
          sort: "asc",
          width: 150
        },
        {
          label: "Unit",
          field: "unit",
          sort: "asc",
          width: 270
        },
        {
          label: "Pre-Requisites",
          field: "prerequisites",
          sort: "asc",
          width: 200
        },
        {
          label: "Semester",
          field: "semester",
          sort: "asc",
          width: 200
        },
        {
          label: "Year",
          field: "year",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        subject: record.subject,
        unit: record.unit,
        prerequisites: record.prerequisites,
        semester: record.sem,
        year: record.year
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };

  render() {
    return (
      <div className="container-fluid bgC">
        {" "}
        <header className="bsBa text-center text-white" id="baheader">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0" style={{ color: "red" }}>
                Bachelor of Science in Business Administration
              </h1>
              <p style={{ color: "black" }}>
                Bachelor of Science in Business Administration (BSBA) programs
                with a concentration in general management are designed to teach
                strategic planning, operations management and human resources,
                as well as team-building and research methods.
              </p>
              <a
                href="#main"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Learn More
              </a>
              <a
                href="#curriculum"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Curriculum
              </a>
            </div>
          </div>
        </header>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={bsba} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">About the Program</h2>
                  <p>
                    Lead business ventures as a highly capable professional with
                    the Bachelor of Science in Business Administration program.
                    Develop an in-depth understanding of the operations of
                    manufacturing, agribusiness, service enterprises, and all
                    the aspects of operation within the organization. Its
                    comprehensive curriculum combined with learning materials
                    from industry partners will ready BSBA graduates for
                    business.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 order-lg-2">
                <div className="p-20">
                  <img
                    className="img-fluid rounded-circle"
                    src={bsba1}
                    alt=""
                  />
                </div>
              </div>
              <div className="col-lg-6 order-lg-1">
                <div className="p-5">
                  <h2 className="display-4">Compentencies</h2>
                  <ul>
                    <li>
                      Manage a strategic business unit for economic
                      sustainability.
                    </li>
                    <li>Innovate business ideas based on emerging industry.</li>
                    <li>Prepare operational plans.</li>
                    <li>
                      Analyze the business environment for strategic direction.
                    </li>
                    <li>Exercise high personal moral and ethical standards.</li>
                    <li>
                      Demonstrate corporate citizenship and social
                      responsibility.
                    </li>
                    <li>Plan and implement business-related activities.</li>
                    <li>
                      Work effectively with other stakeholders and manage
                      conflict in the workplace.
                    </li>
                    <li>
                      Apply information and communication technology (ICT)
                      skills as required by the business environment.
                    </li>
                    <li>
                      Express oneself clearly and communicate effectively with
                      stakeholders
                    </li>
                    <li>
                      Select the proper decision-making tools to critically,
                      analytically and creatively solve problems, and drive
                      results.
                    </li>
                    <li>
                      Apply the basic concepts that underlie each of the
                      functional areas of business (marketing, finance, human
                      resources management, production and operations
                      management, information technology, and strategic
                      management) and employ these concepts in various business
                      situations.
                    </li>
                    <li>
                      Perform the basic functions of management such as
                      planning, organizing, leading, and controlling.
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20">
                  <img
                    className="img-fluid rounded-circle"
                    src={bsba2}
                    alt=""
                  />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Career Opportunities</h2>
                  <ul>
                    <li>Management Trainee</li>
                    <li>Operations Management Assistant</li>
                    <li>Technical Staff</li>
                    <li>Warehouse Assistant</li>
                    <li>Inventory Assistant</li>
                    <li>Project Assistant</li>
                    <li>Logistics Management Personnel</li>
                    <li>Entrepreneur</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="curriculum">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20"></div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Program Curriculum</h2>
                  <p>
                    In education, a curriculum is broadly defined as the
                    totality of student experiences that occur in the
                    educational process. The term often refers specifically to a
                    planned sequence of instruction, or to a view of the
                    student's experiences in terms of the educator's or school's
                    instructional goals.
                  </p>
                  <p>Click the column name to sort the records</p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section className="text-center">{this.table()}</section>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getBsBa
})(BsBa);
