import React, { Component } from "react";
import _ from "lodash";
import { Table } from "react-bootstrap";
import { storage, dbAnnounce } from "../Components/firebaseCloud";
//icons
import { MdDeleteForever } from "react-icons/md";
import { IoIosSave } from "react-icons/io";

//Connect redux
import { connect } from "react-redux";
import {
  getAnnounce,
  saveAnnounce,
  deleteAnnounce,
  updateTitleAnnounce,
  updateDateAnnounce,
  updateDescAnnounce,
  deletePicAnnounce
} from "../Redux/actionFunction";

class SchoolAnnouncements extends Component {
  componentDidMount() {
    ///@@@@@@@@@@@@@@@@@funtion from redux
    this.props.getAnnounce();
  }

  constructor(props) {
    super(props);

    this.state = {
      selectedFile: null,
      title: "",
      date: "",
      description: "",
      records: {},
      pictureUrl: ""
    };
  }
  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  onChangePic(e) {
    this.setState({
      selectedFile: e.target.files[0]
    });

    console.log(this.state.selectedFile);
  }
  //send the files to firebase
  fileUpload = event => {
    event.preventDefault();
    const image = this.state.selectedFile;
    const uploadTask = storage.ref(`imagesAnnounce/${image.name}`).put(image);
    uploadTask.on(
      "state_changed",
      snapshot => {
        //Progress
      },
      error => {
        //Error
        alert(error);
      },
      () => {
        //Complete
        storage
          .ref("imagesAnnounce")
          .child(image.name)
          .getDownloadURL()
          .then(url => {
            console.log(url);
            this.setState({
              url
            });
            const records = {
              title: this.state.title,
              date: this.state.date,
              description: this.state.description,
              pictureUrl: url,
              imagename: image.name
            };
            //this reset the input field ==========
            this.setState({
              title: "",
              description: "",
              date: ""
            });
            //reset the input file field via reference
            this.file.value = "";

            dbAnnounce.push(records);
            alert("Record Updated");
          });
      }
    );
  };

  deleteAnnounceWithPic(key, reference) {
    this.props.deleteAnnounce(key);
    this.props.deletePicAnnounce(reference);
    alert("Record Deleted");
  }

  renderNotes() {
    return (
      <Table className="table table-borderless" size="xl">
        <thead>
          <tr>
            <th>Image</th>
            <th>Title</th>
            <th>Date</th>
            <th>description</th>
          </tr>
        </thead>
        <tbody>
          {_.map(this.props.records, (record, key) => (
            <tr key={key}>
              <td>
                <img className="imgTable" alt="" src={record.pictureUrl} />
              </td>

              <td>{record.title}</td>
              <td>{record.date}</td>
              <td>{record.description}</td>
              <td>
                <button
                  //redux @@@@@@@@@@@@@@@@@@@@@ delete function
                  onClick={() =>
                    this.deleteAnnounceWithPic(key, record.imagename)
                  }
                  className="btn btn-danger"
                >
                  <MdDeleteForever />
                </button>
              </td>
              <td>
                <button
                  className="btn btn-primary col-sm-7 "
                  //redux @@@@@@@@@@@@@@@@@@@@@ update function
                  onClick={() =>
                    this.props.updateTitleAnnounce(key, this.state.title)
                  }
                >
                  Update Title
                </button>
                <br />
                <button
                  className="btn btn-primary col-sm-7 my-1"
                  //redux @@@@@@@@@@@@@@@@@@@@@ update function
                  onClick={() =>
                    this.props.updateDateAnnounce(key, this.state.date)
                  }
                >
                  Update Date
                </button>
                <br />
                <button
                  className="btn btn-primary col-sm-7 "
                  //redux @@@@@@@@@@@@@@@@@@@@@ update function
                  onClick={() =>
                    this.props.updateDescAnnounce(key, this.state.description)
                  }
                >
                  Update description
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
    );
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-3">
            <h1 className="m-3 d-flex justify-content-center">
              Create Announcements
            </h1>
            <div className="m-3 d-flex justify-content-center">
              <form onSubmit={this.fileUpload.bind(this)}>
                <div className="form-group">
                  <input
                    onChange={this.handleChange}
                    value={this.state.title}
                    type="text"
                    name="title"
                    className="form-control no-border"
                    placeholder="Title"
                    required
                  />
                </div>
                <div className="form-group">
                  <input
                    onChange={this.handleChange}
                    value={this.state.date}
                    type="date"
                    name="date"
                    className="form-control no-border"
                    placeholder="date"
                    required
                  />
                </div>
                <div className="form-group">
                  <textarea
                    type="text"
                    name="description"
                    className="form-control no-border"
                    required
                    value={this.state.description}
                    onChange={e => {
                      this.handleChange(e);
                    }}
                    style={{ height: 100 }}
                    placeholder="Description"
                  />{" "}
                  <br />
                  Insert Image here:
                  <input
                    type="file"
                    name="file"
                    className="form-control no-border"
                    required
                    onChange={e => {
                      this.onChangePic(e);
                    }}
                    style={{ width: "250px" }}
                    accept="image/x-png,image/gif,image/jpeg"
                    //reference for reset field
                    ref={ref => (this.file = ref)}
                  />
                </div>
                <div className="form-group">
                  <button
                    className="btn btn-secondary col-sm-12"
                    style={{ width: "100px" }}
                  >
                    <IoIosSave />
                  </button>
                  <br />
                </div>
              </form>
            </div>
          </div>
          <div className="col pt-3">{this.renderNotes()}</div>
        </div>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getAnnounce,
  saveAnnounce,
  deleteAnnounce,
  updateTitleAnnounce,
  updateDateAnnounce,
  updateDescAnnounce,
  deletePicAnnounce
})(SchoolAnnouncements);
