import React, { Component } from "react";

import { connect } from "react-redux";
import { getBsBio } from "../Redux/actionFunction";
import { MDBDataTable } from "mdbreact";
import _ from "lodash";

import Pic from "../Img/BsBio/Bio.png";
import Pic1 from "../Img/BsBio/Bio1.png";
import Pic2 from "../Img/BsBio/Bio2.png";
import Pic3 from "../Img/BsBio/Bio3.png";
import Pic4 from "../Img/BsBio/Bio4.png";

class BsBio extends Component {
  componentDidMount() {
    this.props.getBsBio();
  }

  /////////MDBTable

  table = () => {
    const data = {
      columns: [
        {
          label: "Subject",
          field: "subject",
          sort: "asc",
          width: 150
        },
        {
          label: "Unit",
          field: "unit",
          sort: "asc",
          width: 270
        },
        {
          label: "Pre-Requisites",
          field: "prerequisites",
          sort: "asc",
          width: 200
        },
        {
          label: "Semester",
          field: "semester",
          sort: "asc",
          width: 200
        },
        {
          label: "Year",
          field: "year",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        subject: record.subject,
        unit: record.unit,
        prerequisites: record.prerequisites,
        semester: record.sem,
        year: record.year
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };

  render() {
    return (
      <div className="container-fluid bgC">
        <header className="bsBio text-center text-white" id="bioheader">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0" style={{ color: "red" }}>
                Bachelor of Secondary Education Major in Biological Science
              </h1>
              <p style={{ color: "black" }}>
                The Bachelor in Secondary Education Major in Biological Sciences
                (BSEd  Biological Sciences) is a four-year degree program in
                the Philippines that will prepare you in teaching Biological
                Science subjects in high school.
              </p>
              <a
                href="#main"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Learn More
              </a>
              <a
                href="#curriculum"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Curriculum
              </a>
            </div>
          </div>
          <div className="bg-circle-1 bg-circle"></div>
          <div className="bg-circle-2 bg-circle"></div>
          <div className="bg-circle-3 bg-circle"></div>
          <div className="bg-circle-4 bg-circle"></div>
        </header>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={Pic} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">About the Program</h2>
                  <p>
                    A part of a school or college curriculum concerned with the
                    study of social relationships and the functioning of society
                    and usually made up of courses in history, government,
                    economics, civics, sociology, geography, and anthropology
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 order-lg-2">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={Pic3} alt="" />
                  <img className="img-fluid rounded-circle" src={Pic4} alt="" />
                </div>
              </div>
              <div className="col-lg-6 order-lg-1">
                <div className="p-5">
                  <h2 className="display-4">Compentencies</h2>
                  <ul>
                    <li>
                      Chronologicalproviding insight into time, place and
                      geographic setting
                    </li>
                    <li>
                      Integrateddemonstrating the relationships among
                      disciplines
                    </li>
                    <li>
                      In Depth, giving important periods and events sufficient
                      time for study to ensure student understanding
                    </li>
                    <li>
                      Analyze the business environment for strategic direction.
                    </li>
                    <li>
                      Designed to emphasize an understanding of democracy, an
                      ability to compare it with other political systems, as
                      well as the rights and responsibilities of citizens in a
                      democracy
                    </li>
                    <li>
                      Demonstrate corporate citizenship and social
                      responsibility.
                    </li>
                    <li>Plan and implement business-related activities.</li>
                    <li>Cognizant of the importance of religion in history</li>
                    <li>
                      Reflective of the need for concern for ethics and human
                      rights
                    </li>
                    <li>
                      Express oneself clearly and communicate effectively with
                      stakeholders
                    </li>
                    <li>
                      Select the proper decision-making tools to critically,
                      analytically and creatively solve problems, and drive
                      results.
                    </li>
                    <li>
                      Perform the basic functions of management such as
                      planning, organizing, leading, and controlling.
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={Pic1} alt="" />
                  <img className="img-fluid rounded-circle" src={Pic2} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Career Opportunities</h2>
                  <ul>
                    <li>Goverment</li>
                    <li>Grant Writing</li>
                    <li>Human resources</li>
                    <li>Market Research Analysts</li>
                    <li>Nonprofit organizations</li>
                    <li>technical Writing</li>
                    <li>Training and Development</li>
                    <li>Youth or Afterschool programs and many other areas</li>
                    <li>Sociologists</li>
                    <li>Historians</li>
                    <li>Anthropologists and Archeologists</li>
                    <li>Postsecondary Teachers</li>
                    <li>Political Scientists</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="curriculum">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-5">
                  <div className="table-wrapper-scroll-y my-custom-scrollbar"></div>
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Current Program Curriculum</h2>
                  <p>
                    In education, a curriculum is broadly defined as the
                    totality of student experiences that occur in the
                    educational process. The term often refers specifically to a
                    planned sequence of instruction, or to a view of the
                    student's experiences in terms of the educator's or school's
                    instructional goals.
                  </p>
                  <p>Click the column name to sort the records</p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section>{this.table()}</section>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getBsBio
})(BsBio);
