import React, { Component } from "react";

import { connect } from "react-redux";
import { getExamHigh, deleteExamHigh } from "../Redux/actionFunction";
import { dbExamHigh } from "../Components/firebaseCloud";
import { MDBDataTable, MDBBtn } from "mdbreact";
import _ from "lodash";

//Icons
import { MdDeleteForever } from "react-icons/md";
import { IoIosSave } from "react-icons/io";

class HighSchoolControl extends Component {
  constructor(props) {
    super(props);

    this.state = {
      yearlvl: "",
      section: "",
      subject: "",
      examtype: "",
      notes: "",
      date: ""
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.getExamHigh();
  }
  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  handleSubmit(e) {
    e.preventDefault();

    dbExamHigh.push({
      yearlvl: this.state.yearlvl,
      section: this.state.section,
      subject: this.state.subject,
      examtype: this.state.examtype,
      notes: this.state.notes,
      date: this.state.date
    });
    //reset field for Selection
    this.setState({
      section: "",
      subject: "",
      notes: ""
      // date: ""
    });
    this.examtype.value = this.state.examtype;
    this.yearlvl.value = this.state.yearlvl;
  }

  /////////MDBTable

  table = () => {
    const data = {
      columns: [
        {
          label: "Year Level",
          field: "yearlvl",
          sort: "asc",
          width: 150
        },
        {
          label: "Section",
          field: "section",
          sort: "asc",
          width: 150
        },
        {
          label: "Subject",
          field: "subject",
          sort: "asc",
          width: 270
        },
        {
          label: "Exam Type",
          field: "examtype",
          sort: "asc",
          width: 270
        },
        {
          label: "Notes",
          field: "notes",
          sort: "asc",
          width: 200
        },
        {
          label: "Date",
          field: "date",
          sort: "asc",
          width: 200
        },
        {
          label: "Button",
          field: "handle",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        yearlvl: record.yearlvl,
        section: record.section,
        subject: record.subject,
        examtype: record.examtype,
        notes: record.notes,
        date: record.date,

        handle: (
          <MDBBtn
            className="btn btn-danger"
            onClick={() => this.props.deleteExamHigh(key)}
            size="sm"
          >
            <MdDeleteForever />
          </MDBBtn>
        )
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };

  render() {
    return (
      <div>
        {" "}
        <header className="masthead text-center text-white" id="accheader">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0">
                HighSchool Exam Scheduler
              </h1>
            </div>
          </div>
          <div className="bg-circle-1 bg-circle"></div>
          <div className="bg-circle-2 bg-circle"></div>
          <div className="bg-circle-3 bg-circle"></div>
          <div className="bg-circle-4 bg-circle"></div>
        </header>
        <section id="booksection" className="container-fluid">
          <form
            onSubmit={this.handleSubmit}
            className="m-3 d-flex justify-content-center"
          >
            <div>
              <select
                id="yearlvl"
                className="form-control no-border"
                name="yearlvl"
                onChange={this.handleChange}
                required
                defaultValue={this.state.yearlvl}
                ref={ref => (this.yearlvl = ref)}
              >
                <option value={this.state.yearlvl} disabled>
                  Please select year level
                </option>

                <option name="yearlvl" value="1" onChange={this.handleChange}>
                  1
                </option>
                <option name="yearlvl" value="2" onChange={this.handleChange}>
                  2
                </option>
                <option name="yearlvl" value="3" onChange={this.handleChange}>
                  3
                </option>
                <option name="yearlvl" value="4" onChange={this.handleChange}>
                  4
                </option>
                <option name="yearlvl" value="5" onChange={this.handleChange}>
                  5
                </option>
                <option name="yearlvl" value="6" onChange={this.handleChange}>
                  6
                </option>
                <option name="yearlvl" value="7" onChange={this.handleChange}>
                  7
                </option>
                <option name="yearlvl" value="8" onChange={this.handleChange}>
                  8
                </option>
                <option name="yearlvl" value="9" onChange={this.handleChange}>
                  9
                </option>
                <option name="yearlvl" value="10" onChange={this.handleChange}>
                  10
                </option>
                <option name="yearlvl" value="11" onChange={this.handleChange}>
                  11
                </option>
                <option name="yearlvl" value="12" onChange={this.handleChange}>
                  12
                </option>
              </select>
              <input
                onChange={this.handleChange}
                value={this.state.section}
                type="text"
                name="section"
                className="form-control col-sm-12"
                placeholder="Section"
                required
              />
              <input
                onChange={this.handleChange}
                value={this.state.subject}
                type="text"
                name="subject"
                className="form-control no-border"
                placeholder="Subject"
                required
              />
              <select
                id="examtype"
                className="form-control no-border"
                name="examtype"
                onChange={this.handleChange}
                required
                defaultValue={this.state.examtype}
                ref={ref => (this.examtype = ref)}
              >
                <option value={this.state.examtype} disabled>
                  Please select exam type
                </option>

                <option
                  name="examtype"
                  value="Prelims"
                  onChange={this.handleChange}
                >
                  Prelims
                </option>
                <option
                  name="examtype"
                  value="Midterm"
                  onChange={this.handleChange}
                >
                  Midterm
                </option>
                <option
                  name="examtype"
                  value="Pre-Finals"
                  onChange={this.handleChange}
                >
                  Pre-Finals
                </option>
                <option
                  name="examtype"
                  value="Finals"
                  onChange={this.handleChange}
                >
                  Finals
                </option>
                <option
                  name="examtype"
                  value="Exam"
                  onChange={this.handleChange}
                >
                  Exam...
                </option>
              </select>
              <input
                onChange={this.handleChange}
                value={this.state.notes}
                type="text"
                name="notes"
                className="form-control no-border"
                placeholder="Additional Notes"
              />
              <input
                onChange={this.handleChange}
                value={this.state.date}
                type="date"
                name="date"
                className="form-control no-border"
                placeholder="Date"
                required
              />

              <button type="submit" className="btn btn-secondary col-sm-12">
                <IoIosSave />
              </button>
            </div>
          </form>
        </section>
        <section className="text-center">{this.table()}</section>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getExamHigh,
  deleteExamHigh
})(HighSchoolControl);
