import {
  GET_NOTES,
  GET_ANNOUNCEMENTS,
  GET_COURSE,
  GET_BSCS
} from "../Redux/actionTypes";

//3rd set function

export default function(state = {}, action) {
  switch (action.type) {
    case GET_NOTES:
      return action.payload;
    case GET_ANNOUNCEMENTS:
      return action.payload;
    case GET_COURSE:
      return action.payload;
    case GET_BSCS:
      return action.payload;

    default:
      return state;
  }
}
