import {
  GET_NOTES,
  GET_ANNOUNCEMENTS,
  GET_COURSE,
  GET_BSCS
} from "./actionTypes";
import {
  db,
  dbAnnounce,
  dbCourse,
  dbBscs,
  dbBsAccount,
  dbBsBa,
  dbBsBio,
  dbBsEnglish,
  dbBsFilipino,
  dbBsMath,
  dbBsSs,
  dbBsTle,
  dbLib,
  dbExamCol,
  dbExamHigh,
  dbBsMid,
  storage,
  dbHigh,
  dbGrad,
  dbBsEd
} from "../Components/firebaseCloud";

///2nd create function

//get the data from database
export function getNotes() {
  return dispatch => {
    db.on("value", snapshot => {
      dispatch({
        type: GET_NOTES,
        payload: snapshot.val()
      });
    });
  };
}

//submit data from database
export function saveNotes(record) {
  return dispatch => db.push(record);
}
export function deleteNote(id) {
  // Create a reference to the file to delete

  return dispatch => db.child(id).remove();
}
export function deletePic(img) {
  // Create a reference to delete the picture

  var desertRef = storage.ref("imagesEvent").child(img);

  return dispatch => desertRef.delete();
}
//TODO
export function updateEvent(id, event) {
  return dispatch =>
    db.child(id).update({
      event: event
    });
}
export function updateDate(id, date) {
  return dispatch =>
    db.child(id).update({
      date: date
    });
}
export function updateDesc(id, desc) {
  return dispatch =>
    db.child(id).update({
      description: desc
    });
}
