export function saveAnnounce(record) {
  return dispatch => dbAnnounce.push(record);
}

export function deleteAnnounce(id) {
  return dispatch => dbAnnounce.child(id).remove();
}
export function deletePicAnnounce(img) {
  // Create a reference to delete the picture

  var desertRef = storage.ref("imagesAnnounce").child(img);

  return dispatch => desertRef.delete();
}

