//PROGRAMS====================================================
//BSCS========================================
export function getBSCS() {
  //Important//////////////////////////////////////
  var returnArr = [];
  return dispatch => {
    dbBscs.on("value", snapshot => {
      ////////////////////////////////////////////////////////
      snapshot.forEach(userSnapshot => {
        var item = userSnapshot.val();
        item.key = userSnapshot.key;

        returnArr.push(item);

        //let data = userSnapshot.val();

        //used
        //let arr = [];
        //arr = _.toArray(snapshot.val());

        dispatch({
          type: GET_BSCS,
          payload: snapshot.val()
        });
      });
    });
  };
}

export function deleteData(id) {
  return dispatch => dbBscs.child(id).remove();
}
