import React, { Component } from "react";

import Pic1 from "../Img/HighSchool/Freshman.jpg";
import Pic2 from "../Img/HighSchool/Freshman1.jpg";


export default class HigschoolDesc extends Component {
  render() {
    return (
      <div>
        <div className="container-fluid bgC" id="">
          <header
            className="masthead text-center text-white"
            id="highheaderdesc"
          >
            <div className="masthead-content">
              <div className="container">
                <h1 className="masthead-heading mb-0" style={{ color: "red" }}>
                  High School
                </h1>
                <p>
                  We warmly welcome junior high school completers, senior high
                  school graduates, college transferees, second coursers,
                  foreign applicants, and graduate program applicants to our
                  campuses.
                </p>
                <a
                  href="#main"
                  className="btn btn-primary btn-xl rounded-pill mt-5"
                >
                  Learn More
                </a>
              </div>
            </div>
          </header>

          <section id="main">
            <div className="container">
              <div className="row align-items-center">
                <div className="col-lg-6 order-lg-2">
                  <div className="p-5">
                    <img className="img-fluid rounded-circle" src={""} alt="" />
                  </div>
                </div>
                <div className="col-lg-6 order-lg-1">
                  <div className="p-5">
                    <h2 className="display-4">About</h2>
                    <p>
                      High school is an academic institution providing education
                      more advanced than elementary school or middle school but
                      less advanced than college, typically grades 9, 10, 11 and
                      12. An example of high school is the local public school
                      where 15 year olds attend.
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </section>

          <section>
            <div className="container">
              <div className="row align-items-center">
                <div className="col-lg-6">
                  <div className="p-5">
                    <img className="img-fluid rounded-circle" src={Pic2} alt="" />
                    <img className="img-fluid rounded-circle" src={Pic1} alt="" />
                  </div>
                </div>
                <div className="col-lg-6">
                  <div className="p-5">
                    <h2 className="display-4">What to Expect Freshman Year in High School</h2>
                    <p>
                    Freshman year of high school is a BIG transition. Youll move to a new building, take more intense classes, meet new people, and start seriously thinking about your future.
                    </p>
                    <p>Your Freshman Year Goals</p>
                    <ul>
                      <li>Up Your Study Game</li>
                      <p>High school usually brings more homework, including multi-step projects that will require some planning. If you figure out how to get good grades now, you'll be on the right track for the rest of high school</p>
                      <li>Take Challenging Courses</li>
                      <p>now is the time to work hard and earn great grades. Take a look at your schools course offerings, and make sure you are on track to take the most challenging high school schedule you can and still have success. </p>
                      <li>Find Your People</li>
                      <p>Chances are it's been years since your parents have solved a logarithmic expression. So who can you lean on if you get stuck? Maintain good relationships with your teachers and make sure that youre aware of any services your school offers for academic support.</p>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </section>

          <section id="main">
            <div className="container">
              <div className="row align-items-center">
                <div className="col-lg-6 order-lg-2">
                  <div className="p-5">
                    <img className="img-fluid rounded-circle" src={""} alt="" />
                  </div>
                </div>
                <div className="col-lg-6 order-lg-1">
                  <div className="p-5">
                    <h2 className="display-4">Strand</h2>

                    <p>High School Strand</p>
                    <ul>
                      <li>General Academic (GA)</li>
                      <li>Humanities and Social Sciences (HUMMS)</li>
                      <li>
                        Science, Technology, Engineering and Mathematics (STEM)
                      </li>
                      <li>Accountancy, Business and Management (ABM)</li>
                      <li>Technical-Vocational-Livelihood (TVL) </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    );
  }
}
