import React, { Component } from "react";

import { connect } from "react-redux";
import { getLib } from "../Redux/actionFunction";
import { MDBDataTable } from "mdbreact";
import _ from "lodash";

class Library extends Component {
  componentDidMount() {
    this.props.getLib();
  }
  /////////MDBTable

  table = () => {
    const data = {
      columns: [
        {
          label: "Id",
          field: "id",
          sort: "asc",
          width: 150
        },
        {
          label: "Book Title",
          field: "title",
          sort: "asc",
          width: 270
        },
        {
          label: "Author",
          field: "author",
          sort: "asc",
          width: 200
        },
        {
          label: "Date",
          field: "date",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        id: record.id,
        title: record.title,
        author: record.author,
        date: record.date
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };

  render() {
    return (
      <div className="container-fluid pt-5 anyClass centered bgLib">
        <div className="container-fluid" id="lib">
          <h3>Libray Book List</h3>
          <hr />
          <p>
            A school library is a library within a school where students, staff,
            and often, parents of a public or private school have access to a
            variety of resources.
          </p>
          <p className="lead">This section list the books owned.</p>
          <br />

          <div>
            <section className="container-fluid">{this.table()}</section>
          </div>
        </div>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getLib
})(Library);
