import React, { Component } from "react";
import "../css/one-page-wonder.min.css";

import { connect } from "react-redux";
import { getBSMidWife } from "../Redux/actionFunction";
import { MDBDataTable } from "mdbreact";
import _ from "lodash";

import Pic from "../Img/BsMid/Image.png";
import Pic1 from "../Img/BsMid/Image1.png";
import Pic2 from "../Img/BsMid/Image2.png";
import Pic3 from "../Img/BsMid/Image3.png";

class BsMidwifery extends Component {
  componentDidMount() {
    this.props.getBSMidWife();
  }
  /////////MDBTable

  table = () => {
    const data = {
      columns: [
        {
          label: "Subject",
          field: "subject",
          sort: "asc",
          width: 150
        },
        {
          label: "Unit",
          field: "unit",
          sort: "asc",
          width: 270
        },
        {
          label: "Pre-Requisites",
          field: "prerequisites",
          sort: "asc",
          width: 200
        },
        {
          label: "Semester",
          field: "semester",
          sort: "asc",
          width: 200
        },
        {
          label: "Year",
          field: "year",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        subject: record.subject,
        unit: record.unit,
        prerequisites: record.prerequisites,
        semester: record.sem,
        year: record.year
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };

  render() {
    return (
      <div className="container-fluid  bgC">
        <header className="bsMid text-center text-white" id="midheader">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0" style={{ color: "red" }}>
                Bachelor of Science in Midwifery
              </h1>
              <p style={{ color: "black" }}>
                The Bachelor of Science in Midwifery (BSM) is a four-year
                ladderized degree program designed to equip students with
                knowledge and skills in helping women in their childbearing
                cycle and birthing process. Students are trained to learn
                maternity care during pregnancy,labor, delivery, OB-GYN
                emergencies, and post-partum as well as caring for infants and
                children.
              </p>
              <a
                href="#main"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Learn More
              </a>
              <a
                href="#curriculum"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Curriculum
              </a>
            </div>
          </div>
          <div className="bg-circle-1 bg-circle"></div>
          <div className="bg-circle-2 bg-circle"></div>
          <div className="bg-circle-3 bg-circle"></div>
          <div className="bg-circle-4 bg-circle"></div>
        </header>

        {/* Left Section */}
        <section>
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-5">
                  {/* Your Image Here */}
                  <img className="img-fluid rounded-circle" src={Pic} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">About this Program</h2>
                  <p>
                    Diploma in Midwifery is a two-year program consisting of
                    general education and professional courses which prepare
                    students for entry-level midwifery competencies. This 2-year
                    direct entry program includes Clinical Practicum in
                    Foundations of Midwifery, Normal Obstetrics and Care of New
                    Born, Introduction to High Risk Obstetrics, Basic Care of
                    Infants and Feeding, Basic Family Planning, Primary Health
                    Care and Midwifery Ethics, Law and Practice. Graduates of
                    the program are qualified to take the government licensure
                    examination.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section>
        {/* Right Section */}
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 order-lg-2">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={Pic1} alt="" />
                  <img className="img-fluid rounded-circle" src={Pic2} alt="" />
                </div>
              </div>
              <div className="col-lg-6 order-lg-1">
                <div className="p-5">
                  <h2 className="display-4">Compentencies</h2>
                  <ul>
                    <li>
                      Midwifery education is based on an understanding of health
                      sciences theory and clinical preparation that shapes
                      knowledge, judgment, and skills deemed necessary to
                      provide primary health care management to women and
                      newborns.
                    </li>
                    <li>
                      Recognition of menarche, pregnancy, birth, and menopause
                      as normal physiologic and developmental processes
                    </li>
                    <li>
                      Knowledge of national and international issues and trends
                      in women's health and maternal/newborn care
                    </li>
                    <li>
                      Broad understanding of the bioethics related to the care
                      of women, newborns, and families
                    </li>
                    <li>
                      Assume responsibility for the safe and efficient
                      implementation of a plan of care that includes the
                      provision of treatments and interventions as indicated
                    </li>
                    <li>
                      Management strategies and therapeutics for the treatment
                      of common health problems and deviations from normal of
                      women, including infections, self-limited conditions, and
                      mild and/or stable presentations of chronic conditions,
                      utilizing consultation, collaboration, and/or referral to
                      appropriate health car services as indicated.
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        {/* Left Section */}
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={Pic3} alt="" />
                  <img className="img-fluid rounded-circle" src={""} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Career Opportunities</h2>
                  <ul>
                    <li>OB-GYNs</li>
                    <li>Childbirth educators</li>
                    <li>Physicians offices</li>
                    <li>Medical centers</li>
                    <li>Medical device manufacturers</li>
                    <li>Government offices</li>
                    <li>Birthing centers</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="curriculum">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-5">
                  <div className="table-wrapper-scroll-y my-custom-scrollbar"></div>
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Current Program Curriculum</h2>
                  <p>
                    In education, a curriculum is broadly defined as the
                    totality of student experiences that occur in the
                    educational process. The term often refers specifically to a
                    planned sequence of instruction, or to a view of the
                    student's experiences in terms of the educator's or school's
                    instructional goals.
                  </p>
                  <p>Click the column name to sort the records</p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section>{this.table()}</section>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getBSMidWife
})(BsMidwifery);
