import React, { Component } from "react";
import pic from "../Img/GradeSchool/play1.JPG";
import pic1 from "../Img/GradeSchool/play2.JPG";
export default class GradeSchool extends Component {
  render() {
    return (
      <div>
        <div>
          <div className="container-fluid bgC" id="">
            <header
              className="bsGrade text-center text-white"
              id="gradeheaderdesc"
            >
              <div className="masthead-content">
                <div className="container">
                  <h1
                    className="masthead-heading mb-0"
                    style={{ color: "red" }}
                  >
                    GradeSchool
                  </h1>
                  <p>Elementary school is defined as a period of formal education following pre-school but before high school. It usually encompasses grades 1-6 and students learn basic skills in areas such as reading, writing and math.</p>
                  <a
                    href="#main"
                    className="btn btn-primary btn-xl rounded-pill mt-5"
                  >
                    Learn More
                  </a>
                </div>
              </div>
            </header>

            <section id="main">
              <div className="container">
                <div className="row align-items-center">
                  <div className="col-lg-6 order-lg-2">
                    <div className="p-5">
                      <img className="img-fluid rounded-circle" src={pic1} alt="" />
                    </div>
                  </div>
                  <div className="col-lg-6 order-lg-1">
                    <div className="p-5">
                      <h2 className="display-4">About</h2>
                      <p>The Dr. Francisco L. Calingasan Grade School is the unit of the Dr. Francisco L. Calingasan educational system that provides elementary education. Its immediate aim is to provide learning tools, skills, habits, attitudes, and values that will be needed by the students in pursuing studies in the secondary and tertiary levels, and beyond.
The Dr. Francisco L. Calingasan provides a well-balanced program of activities and experiences to develop academic competence and  commitment. It strives to form its students into young men who will become leaders in the service of God and mankind.</p>
                    </div>
                  </div>
                </div>
              </div>
            </section>

            <section>
              <div className="container">
                <div className="row align-items-center">
                  <div className="col-lg-6">
                    <div className="p-5">
                      <img className="img-fluid rounded-circle" src={pic} alt="" />
                    </div>
                  </div>
                  <div className="col-lg-6">
                    <div className="p-5">
                      <h2 className="display-4">Philippine Elementary Learning Competencies (PELC)</h2>

                      
                      {/* TODO */}
                      <p> 
The PELC is a listing of expected outcomes in the four phases of Communication Arts, namely, listening,speaking, reading and writing. The expected outcomes are stated in behavioral terms hierarchicallyarranged from the simplest to the most complex. The expected outcomes (skills) for each component arein matrix form per grade level.With this arrangement, teachers can easily pick out objectives from each phase in preparing his/her lessons to show integration within learning areas. The teacher is free to decide whether to have the four components or only two or three components integrated in a days lesson</p>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </div>
        </div>
      </div>
    );
  }
}
