import React, { Component } from "react";

export default class MissionModal extends Component {
  render() {
    return (
      <div
        className="modal fade"
        id="missionModal"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="exampleModalCenterTitle"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLongTitle">
                Mission
              </h5>

              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              <div>
                <p>
                  DFLCMCFI is committed to develop quality graduates,
                  well-equipped with knowledge and skils imbued with flipino
                  values by providing learning environment for the the
                  utilization of knowledge through innovative education and
                  community partnerships to transforms individuals and be part
                  of the productive and responsible citizens.
                </p>
              </div>
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                data-dismiss="modal"
              >
                Close
              </button>
              {/* <button type="button" className="btn btn-primary">
                Save changes
              </button> */}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
