import React, { Component } from "react";

import { connect } from "react-redux";
import { getBsEnglish } from "../Redux/actionFunction";
import { MDBDataTable } from "mdbreact";
import _ from "lodash";

import Pic from "../Img/BsEnglish/Image.png";
import Pic1 from "../Img/BsEnglish/Image1.png";
import Pic2 from "../Img/BsEnglish/Image2.png";
import Pic3 from "../Img/BsEnglish/Image3.png";

class BsEnglish extends Component {
  componentDidMount() {
    this.props.getBsEnglish();
  }
  /////////MDBTable

  table = () => {
    const data = {
      columns: [
        {
          label: "Subject",
          field: "subject",
          sort: "asc",
          width: 150
        },
        {
          label: "Unit",
          field: "unit",
          sort: "asc",
          width: 270
        },
        {
          label: "Pre-Requisites",
          field: "prerequisites",
          sort: "asc",
          width: 200
        },
        {
          label: "Semester",
          field: "semester",
          sort: "asc",
          width: 200
        },
        {
          label: "Year",
          field: "year",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        subject: record.subject,
        unit: record.unit,
        prerequisites: record.prerequisites,
        semester: record.sem,
        year: record.year
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };

  render() {
    return (
      <div className="container-fluid bgC">
        <header className="bsEng text-center text-white" id="engheader">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0" style={{ color: "red" }}>
                Bachelor of Secondary Education Major in English
              </h1>
              <p style={{ color: "black" }}>
                The Bachelor of Science in Education major in English program
                (BSED-ENG) is designed to provide students with training for
                teaching English for both elementary and high school education,
                and prepare them to become qualified English teachers upon
                graduation.
              </p>
              <a
                href="#main"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Learn More
              </a>
              <a
                href="#curriculum"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Curriculum
              </a>
            </div>
          </div>
          <div className="bg-circle-1 bg-circle"></div>
          <div className="bg-circle-2 bg-circle"></div>
          <div className="bg-circle-3 bg-circle"></div>
          <div className="bg-circle-4 bg-circle"></div>
        </header>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={Pic} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">About the Program</h2>
                  <p>
                    BS English (Applied Linguistics) is a four-year degree
                    program that has been designed with a special focus on
                    advancing academic and professional development of students
                    who want to gain knowledge of applied linguistics and apply
                    it to practical problems related to language in use and
                    language teaching.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 order-lg-2">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={Pic1} alt="" />
                  <img className="img-fluid rounded-circle" src={Pic2} alt="" />
                </div>
              </div>
              <div className="col-lg-6 order-lg-1">
                <div className="p-5">
                  <h2 className="display-4">Compentencies</h2>
                  <ul>
                    <li>Interacting Well with Students</li>
                    <li>Creating a Learning Environment</li>
                    <li>Good at Lesson Plan Design</li>
                    <li>Able to Use Varied Teaching Strategies.</li>
                    <li>Able to Assess</li>
                    <li>Able to Identify Student Needs</li>
                    <li>Good at Communication.</li>
                    <li>Able to Collaborate</li>
                    <li>MAintainging a professional Appearance</li>
                    <li> Demonstrating a Commitment to the Profession</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={Pic3} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Career Opportunities</h2>
                  <ul>
                    <li>English Teacher</li>
                    <li>Assistant Teacher</li>
                    <li>Call Center Agent</li>
                    <li>Customer service jobs</li>
                    <li>English Tutor</li>
                    <li>Technical Writer</li>
                    <li>Proofreader</li>
                    <li>Editor</li>
                    <li>Academician</li>
                    <li>Reseacher</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="curriculum">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-5">
                  <div className="table-wrapper-scroll-y my-custom-scrollbar"></div>
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Current Program Curriculum</h2>
                  <p>
                    In education, a curriculum is broadly defined as the
                    totality of student experiences that occur in the
                    educational process. The term often refers specifically to a
                    planned sequence of instruction, or to a view of the
                    student's experiences in terms of the educator's or school's
                    instructional goals.
                  </p>
                  <p>Click the column name to sort the records</p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section className="text-center">{this.table()}</section>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getBsEnglish
})(BsEnglish);
