import React, { Component } from "react";
import { connect } from "react-redux";
import { getGrad } from "../Redux/actionFunction";

import { MDBDataTable } from "mdbreact";
import _ from "lodash";

class GraduatesTable extends Component {
  constructor(props) {
    super(props);

    this.state = {
      name: "",
      course: "",
      yearlvl: "",
      remarks: ""
    };
  }
  componentDidMount() {
    this.props.getGrad();
  }

  /////////MDBTable

  table = () => {
    const data = {
      columns: [
        {
          label: "Full name",
          field: "name",
          sort: "asc",
          width: 150
        },
        {
          label: "Year level",
          field: "yearlvl",
          sort: "asc",
          width: 270
        },
        {
          label: "Course",
          field: "course",
          sort: "asc",
          width: 200
        },
        {
          label: "Remarks",
          field: "remarks",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        name: record.name,
        yearlvl: record.yearlvl,
        course: record.course,
        remarks: record.remarks
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };

  render() {
    return (
      <div className="bgGrad">
        <header className="GradHeader text-center text-white">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0" style={{ color: "white" }}>
                Candidates for Graduation
              </h1>
              <p>Be bold, be courageous, be your best.</p>
            </div>
          </div>
        </header>

        <section className="text-center">{this.table()}</section>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getGrad
})(GraduatesTable);
