import React, { Component } from "react";
import _ from "lodash";
import { Table } from "react-bootstrap";
import { storage, db } from "../Components/firebaseCloud";

//icons
import { MdDeleteForever } from "react-icons/md";
import { IoIosSave } from "react-icons/io";

//Connect redux
import { connect } from "react-redux";
import {
  getNotes,
  saveNotes,
  deleteNote,
  updateEvent,
  updateDate,
  updateDesc,
  deletePic
} from "../Redux/actionFunction";

class SchoolEvents extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedFile: null,
      event: "",
      date: "",
      description: "",
      records: {},
      url: "",
      pictureUrl: ""
    };
    this.deleteRecord = this.deleteRecord.bind(this);
  }

  //lifecycle
  //get the function working as soon as the  program starts
  componentDidMount() {
    ///@@@@@@@@@@@@@@@@@funtion from redux
    this.props.getNotes();
  }

  //set the value of the targetstate in the textbox
  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };
  //submit the data, and send to the database

  fileUpload = event => {
    event.preventDefault();
    const image = this.state.selectedFile;
    const uploadTask = storage.ref(`imagesEvent/${image.name}`).put(image);
    uploadTask.on(
      "state_changed",
      snapshot => {
        //Progress
      },
      error => {
        //Error
        alert(error);
      },
      () => {
        //Complete
        storage
          .ref("imagesEvent")
          .child(image.name)
          .getDownloadURL()
          .then(url => {
            console.log(url);
            this.setState({
              url
            });
            const records = {
              event: this.state.event,
              date: this.state.date,
              description: this.state.description,
              pictureUrl: url,
              imagename: image.name
            };
            console.log(image.name);

            //this reset the input field ==========
            this.setState({
              event: "",
              description: "",
              date: ""
            });
            //reset the input file field via reference
            this.file.value = "";

            db.push(records);
            alert("Record Updated");
          });
      }
    );
  };

  //change pic event
  onChangePic(e) {
    this.setState({
      selectedFile: e.target.files[0]
    });

    console.log(this.state.selectedFile);
  }
  //Delete the record and picture
  deleteRecord(key, reference) {
    this.props.deletePic(reference);
    this.props.deleteNote(key);
    alert("Record Deleted");
  }

  //render notes, Get every element on the object
  renderNotes() {
    //parameters(object,(obj,key))

    return _.map(this.props.records, (record, key) => {
      return (
        //Table view
        <Table className="table table-borderless" size="xl" key={key}>
          <thead>
            <tr>
              <th>Image</th>

              <th>Event</th>
              <th>Date</th>
              <th>description</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>
                <img className="imgTable" alt="" src={record.pictureUrl} />
              </td>

              <td>{record.event}</td>
              <td>{record.date}</td>
              <td>{record.description}</td>
              <td>
                <button
                  className="btn btn-danger "
                  //redux @@@@@@@@@@@@@@@@@@@@@ delete function
                  onClick={() => this.deleteRecord(key, record.imagename)}
                >
                  <MdDeleteForever />
                </button>
              </td>

              <td>
                <button
                  className="btn btn-primary "
                  style={{ width: "100px" }}
                  //redux @@@@@@@@@@@@@@@@@@@@@ update function
                  onClick={() => this.props.updateEvent(key, this.state.event)}
                >
                  Update Event
                </button>
                <br />
                <button
                  className="btn btn-primary my-1 "
                  style={{ width: "100px" }}
                  //redux @@@@@@@@@@@@@@@@@@@@@ update function
                  onClick={() => this.props.updateDate(key, this.state.date)}
                >
                  Update Date
                </button>
                <br />
                <button
                  className="btn btn-primary "
                  style={{ width: "100px" }}
                  //redux @@@@@@@@@@@@@@@@@@@@@ update function
                  onClick={() =>
                    this.props.updateDate(key, this.state.description)
                  }
                >
                  Update description
                </button>
              </td>
            </tr>
          </tbody>
        </Table>
      );
    });
  }

  render() {
    return (
      <div className="container-fluid centered">
        <div className="row">
          <div className="col-3">
            <h1 className="m-3 d-flex justify-content-center">Create Event</h1>
            <div>
              <form onSubmit={this.fileUpload.bind(this)}>
                <div className="form-group">
                  <input
                    onChange={this.handleChange}
                    value={this.state.event}
                    type="text"
                    name="event"
                    className="form-control no-border"
                    placeholder="Event"
                    required
                  />
                </div>
                <div className="form-group">
                  <input
                    onChange={this.handleChange}
                    value={this.state.date}
                    type="date"
                    name="date"
                    className="form-control no-border"
                    placeholder="date"
                    required
                  />
                </div>
                <div className="form-group">
                  <textarea
                    onChange={this.handleChange}
                    value={this.state.description}
                    type="text"
                    name="description"
                    className="form-control no-border "
                    placeholder="description"
                    required
                    style={{ height: 100 }}
                  />
                  <input
                    type="file"
                    name="file"
                    className="form-control no-border"
                    required
                    onChange={e => {
                      this.onChangePic(e);
                    }}
                    style={{ width: "250px" }}
                    accept="image/x-png,image/gif,image/jpeg"
                    //reference for reset field
                    ref={ref => (this.file = ref)}
                  />
                </div>
                <div className="form-group">
                  <button className="btn btn-secondary col-sm-12">
                    {" "}
                    <IoIosSave />
                  </button>
                  <br />
                </div>
              </form>
            </div>
          </div>
          <div className="col pt-3">{this.renderNotes()}</div>
        </div>
      </div>
    );
  }
}

//set the reducers
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getNotes,
  saveNotes,
  deleteNote,
  updateEvent,
  updateDate,
  updateDesc,
  deletePic
})(SchoolEvents);
