import React, { Component } from "react";
import bsa from "../Img/BsA/bsa.jpg";
import bsa1 from "../Img/BsA/bsa1.png";
import bsa2 from "../Img/BsA/bsa2.jpg";
import bsa3 from "../Img/BsA/bsa3.jpg";

import { MDBDataTable } from "mdbreact";
import _ from "lodash";

import { connect } from "react-redux";
import { getBsAccount } from "../Redux/actionFunction";

class BsAccount extends Component {
  componentDidMount() {
    this.props.getBsAccount();
  }
  /////////MDBTable

  table = () => {
    const data = {
      columns: [
        {
          label: "Subject",
          field: "subject",
          sort: "asc",
          width: 150
        },
        {
          label: "Unit",
          field: "unit",
          sort: "asc",
          width: 270
        },
        {
          label: "Pre-Requisites",
          field: "prerequisites",
          sort: "asc",
          width: 200
        },
        {
          label: "Semester",
          field: "semester",
          sort: "asc",
          width: 200
        },
        {
          label: "Year",
          field: "year",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        subject: record.subject,
        unit: record.unit,
        prerequisites: record.prerequisites,
        semester: record.sem,
        year: record.year
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };

  render() {
    return (
      <div className="container-fluid bgC">
        <header className="bsAcc text-center text-white" id="accheader">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0" style={{ color: "red" }}>
                Bachelor of Science in Accountancy
              </h1>
              <p style={{ color: "black" }}>
                A Bachelor of Science (BS) in Accounting program trains students
                to manage a company's financial information and tax documents.
                It can serve as a first step toward licensure as a Certified
                Public Accountant (CPA).
              </p>
              <a
                href="#main"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Learn More
              </a>
              <a
                href="#curriculum"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Curriculum
              </a>
            </div>
          </div>
          <div className="bg-circle-1 bg-circle"></div>
          <div className="bg-circle-2 bg-circle"></div>
          <div className="bg-circle-3 bg-circle"></div>
          <div className="bg-circle-4 bg-circle"></div>
        </header>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 order-lg-2">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={bsa} alt="" />
                </div>
              </div>
              <div className="col-lg-6 order-lg-1">
                <div className="p-5">
                  <h2 className="display-4">About Bs in Accountancy</h2>
                  <p>
                    About the Program Master the language of business with the
                    Bachelor of Science in Accountancy program. Train in-depth
                    on financial accounting and reporting, cost accounting and
                    management, accountancy and control, taxation, and
                    accounting information systems. BSA graduates will be
                    qualified to take the board exams and become Certified
                    Public Accountants (CPA).
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section>
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={bsa1} alt="" />
                  <br />
                  <br />
                  <br />

                  <img className="img-fluid rounded-circle" src={bsa2} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Description</h2>
                  <p>
                    As described in the CMO, Accounting professionals are
                    involved in providing assurance and audit services for
                    statutory financial reporting; tax-related services;
                    management advisory services, specifically partnering in
                    management decision-making, devising planning, and
                    performance and control systems; and providing expertise in
                    financial reporting and control to assist various
                    stakeholders in making decisions. The goal of the BSA
                    Program is to produce competent and ethical public
                    accounting professionals who possess the knowledge, skills,
                    and attitudes that will enable them to continue to learn and
                    adapt to change throughout their professional lives. The
                    following table lists the sets of knowledge, skills, and
                    attitudes expected to be developed in the learners:
                  </p>
                  <p>Knowledge:</p>
                  <ul>
                    <li>Business concepts, processes, and procedures</li>
                    <li>Accounting functions and reporting frameworks</li>
                    <li>Auditing standards and procedures</li>
                    <li>Information and communication technology</li>

                    <li>Regulatory framework</li>
                    <li>Catholic, Christian, and ethical teachings</li>
                  </ul>
                  <p>Skills:</p>
                  <ul>
                    <li>Presentation skills</li>
                    <li>Social skills and cross-cultural skills</li>
                    <li>Accounting skills</li>
                    <li>Leadership and collaboration</li>
                    <li>Critical thinking and problem solving skills</li>
                    <li>Documentation and report writing skills</li>
                    <li>Life-long learning skills</li>
                    <li>Reporting skills (financial, management and tax)</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 order-lg-2">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={bsa3} alt="" />
                </div>
              </div>
              <div className="col-lg-6 order-lg-1">
                <div className="p-5">
                  <h2 className="display-4">Career Opportunities</h2>
                  <ul>
                    <li>Accounting Instructor</li>
                    <li>Revenue Officer</li>
                    <li>Audit Examiner</li>
                    <li>Credit Analyst</li>
                    <li>Budget Analyst/Officer</li>
                    <li>Financial Analyst</li>
                    <li>Tax Accounting Staff</li>
                    <li>Accountancy Staff</li>
                    <li>Cost Analyst/Accountant</li>
                    <li>Investment Analyst</li>
                    <li>Junior Analyst</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="curriculum">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 order-lg-2">
                <div className="p-20"></div>
              </div>
              <div className="col-lg-6 order-lg-1">
                <div className="p-5">
                  <h2 className="display-4">Current Program Curriculum</h2>
                  <p>
                    In education, a curriculum is broadly defined as the
                    totality of student experiences that occur in the
                    educational process. The term often refers specifically to a
                    planned sequence of instruction, or to a view of the
                    student's experiences in terms of the educator's or school's
                    instructional goals.
                  </p>
                  <p>Click the column name to sort the records</p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section className="text-center">{this.table()}</section>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getBsAccount
})(BsAccount);
