import React, { Component } from "react";

import { connect } from "react-redux";
import { getLib, deleteBook } from "../Redux/actionFunction";
import { dbLib } from "../Components/firebaseCloud";
import { MDBDataTable, MDBBtn } from "mdbreact";
import _ from "lodash";
//icons
import { MdDeleteForever } from "react-icons/md";
import { IoIosSave } from "react-icons/io";

class LibraryControl extends Component {
  constructor(props) {
    super(props);

    this.state = {
      id: "",
      title: "",
      author: "",
      date: ""
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.getLib();
  }
  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  handleSubmit(e) {
    e.preventDefault();

    //Check if there is existing record of ID
    dbLib
      .orderByChild("id")
      .equalTo(this.state.id)
      .once("value", snapshot => {
        if (snapshot.exists()) {
          alert("I.D. Record Exist!");
          this.setState({
            id: "",
            title: "",
            author: ""
            // date: ""
          });
        } else {
          alert("Record Updated!");
          dbLib.push({
            id: this.state.id,
            title: this.state.title,
            author: this.state.author,
            date: this.state.date
          });
          //reset field for Selection
          this.setState({
            id: "",
            title: "",
            author: ""
            // date: ""
          });
        }
      });
  }

  /////////MDBTable

  table = () => {
    const data = {
      columns: [
        {
          label: "Id",
          field: "id",
          sort: "asc",
          width: 150
        },
        {
          label: "Book Title",
          field: "title",
          sort: "asc",
          width: 270
        },
        {
          label: "Author",
          field: "author",
          sort: "asc",
          width: 200
        },
        {
          label: "Date",
          field: "date",
          sort: "asc",
          width: 200
        },
        {
          label: "Button",
          field: "handle",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        id: record.id,
        title: record.title,
        author: record.author,
        date: record.date,

        handle: (
          <MDBBtn
            className="btn btn-danger"
            onClick={() => this.props.deleteBook(key)}
            size="sm"
          >
            <MdDeleteForever />
          </MDBBtn>
        )
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };

  render() {
    return (
      <div>
        {" "}
        <header className="masthead text-center text-white" id="accheader">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0">Library</h1>
              <p>
                A school library is a library within a school where students,
                staff, and often, parents of a public or private school have
                access to a variety of resources.
              </p>
            </div>
          </div>
          <div className="bg-circle-1 bg-circle"></div>
          <div className="bg-circle-2 bg-circle"></div>
          <div className="bg-circle-3 bg-circle"></div>
          <div className="bg-circle-4 bg-circle"></div>
        </header>
        <section id="booksection" className="container-fluid">
          <form
            onSubmit={this.handleSubmit}
            className="m-3 d-flex justify-content-center"
          >
            <div>
              <input
                onChange={this.handleChange}
                value={this.state.id}
                type="number"
                name="id"
                className="form-control col-sm-12"
                placeholder="Book I.D."
                required
              />
              <input
                onChange={this.handleChange}
                value={this.state.title}
                type="text"
                name="title"
                className="form-control no-border"
                placeholder="Book Title"
                required
              />
              <input
                onChange={this.handleChange}
                value={this.state.author}
                type="text"
                name="author"
                className="form-control no-border"
                placeholder="Book Author"
                required
              />
              <input
                onChange={this.handleChange}
                value={this.state.date}
                type="date"
                name="date"
                className="form-control no-border"
                placeholder="Date"
                required
              />

              <button type="submit" className="btn btn-secondary col-sm-12">
                <IoIosSave />
              </button>
            </div>
          </form>
        </section>
        <section className="text-center">{this.table()}</section>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getLib,
  deleteBook
})(LibraryControl);
