import React, { Component } from "react";

import { connect } from "react-redux";
import { getBsMath } from "../Redux/actionFunction";
import { MDBDataTable } from "mdbreact";
import _ from "lodash";

import Pic from "../Img/BsMath/Image.png";
import Pic1 from "../Img/BsMath/Image1.png";
import Pic2 from "../Img/BsMath/Image2.png";
import Pic3 from "../Img/BsMath/Image3.png";
import Pic4 from "../Img/BsMath/Image4.png";

class BsMath extends Component {
  componentDidMount() {
    this.props.getBsMath();
  }

  /////////MDBTable

  table = () => {
    const data = {
      columns: [
        {
          label: "Subject",
          field: "subject",
          sort: "asc",
          width: 150
        },
        {
          label: "Unit",
          field: "unit",
          sort: "asc",
          width: 270
        },
        {
          label: "Pre-Requisites",
          field: "prerequisites",
          sort: "asc",
          width: 200
        },
        {
          label: "Semester",
          field: "semester",
          sort: "asc",
          width: 200
        },
        {
          label: "Year",
          field: "year",
          sort: "asc",
          width: 200
        },
        {
          label: "Button",
          field: "handle",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        subject: record.subject,
        unit: record.unit,
        prerequisites: record.prerequisites,
        semester: record.sem,
        year: record.year
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };

  render() {
    return (
      <div className="container-fluid  bgC">
        <header className="bsMath text-center text-white" id="mathheader">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0" style={{ color: "red" }}>
                Bachelor of Secondary Education Major in Mathematics
              </h1>
              <p style={{ color: "black" }}>
                The Bachelor of Secondary Education Major in Mathematics (BSEd 
                Math) is a four-year degree program in the Philippines that will
                prepare you in teaching the different branches of Mathematics
                such as algebra, geometry, trigonometry, calculus, and
                statistics in High Sch
              </p>
              <a
                href="#main"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Learn More
              </a>
              <a
                href="#curriculum"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Curriculum
              </a>
            </div>
          </div>
          <div className="bg-circle-1 bg-circle"></div>
          <div className="bg-circle-2 bg-circle"></div>
          <div className="bg-circle-3 bg-circle"></div>
          <div className="bg-circle-4 bg-circle"></div>
        </header>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={Pic} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">About the Program</h2>
                  <p>
                    A typical mathematics degree program involves a combination
                    of pure (theory and abstract) mathematics and applied
                    (practical application to the world) mathematics. Some
                    institutions also offer pure and applied mathematics as
                    separate degrees, so you can choose to focus on just one.
                    Mathematics is also often offered as a joint-honors degree,
                    paired with subjects including business management, computer
                    science, economics, finance, history, music, philosophy,
                    physics, sports science and statistics.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 order-lg-2">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={Pic1} alt="" />
                  <img className="img-fluid rounded-circle" src={Pic2} alt="" />
                </div>
              </div>
              <div className="col-lg-6 order-lg-1">
                <div className="p-5">
                  <h2 className="display-4">Compentencies</h2>
                  <ul>
                    <li>
                      Communication  Incoming: reading, decoding, interpreting
                      statements and mathematical information. Outgoing:
                      explaining, presenting and arguing.
                    </li>
                    <li>
                      Mathematising  Transform a real world problem into a
                      mathematical problem. Interpret mathematical objects or
                      information in relation to the situation represented.{" "}
                    </li>
                    <li>
                      Representation  Devising or using depictions of
                      mathematical objects or relationships: equations,
                      formulae, graphs, tables, diagrams, textual descriptions.{" "}
                    </li>
                    <li>
                      Reasoning and argument  Logically rooted thought
                      processes that explore and link problem elements to make
                      inferences from them; or to check a given justification;
                      or to provide a justification.
                    </li>
                    <li>
                      Strategic thinking  Selecting or devising, and
                      implementing, a mathematical strategy to solve problems
                      arising from the task or context.
                    </li>
                    <li>
                      Using symbolic, formal and technical language and
                      operations  Understanding, manipulating, and making use
                      of symbolic expressions; using constructs based on
                      definitions, rules and conventions, formal systems.
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="main">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={Pic3} alt="" />
                  <img className="img-fluid rounded-circle" src={Pic4} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Career Opportunities</h2>
                  <ul>
                    <li>Engineering firms</li>
                    <li>Software companies</li>
                    <li>Real estate firms</li>
                    <li>Software companies</li>
                    <li>Medical device manufacturers</li>
                    <li>Airlines and other transportation service providers</li>
                    <li>Logistics Management Personnel</li>
                    <li>Financial institutions</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="curriculum">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-5">
                  <div className="table-wrapper-scroll-y my-custom-scrollbar"></div>
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Current Program Curriculum</h2>
                  <p>
                    In education, a curriculum is broadly defined as the
                    totality of student experiences that occur in the
                    educational process. The term often refers specifically to a
                    planned sequence of instruction, or to a view of the
                    student's experiences in terms of the educator's or school's
                    instructional goals.
                  </p>
                  <p>Click the column name to sort the records</p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section className="text-center">{this.table()}</section>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getBsMath
})(BsMath);
