import React, { Component } from "react";

export default class Mission extends Component {
  render() {
    return (
      <div className="container-fluid">
        <header className="masthead text-center text-white">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0">
                Mission, Vision of DFLCMCFI
              </h1>
              <a
                href="#main"
                className="btn btn-primary btn-xl rounded-pill mt-5"
              >
                Learn More
              </a>
            </div>
          </div>
          <div className="bg-circle-1 bg-circle"></div>
          <div className="bg-circle-2 bg-circle"></div>
          <div className="bg-circle-3 bg-circle"></div>
          <div className="bg-circle-4 bg-circle"></div>
        </header>
        <section id="mission">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={""} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Mission</h2>
                  <p>
                    DFLCMCFI is committed to develop quality graduates,
                    well-equipped with knowledge and skils imbued with flipino
                    values by providing learning environment for the the
                    utilization of knowledge through innovative education and
                    community partnerships to transforms individuals and be part
                    of the productive and responsible citizens.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="vission">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6 order-lg-2">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={""} alt="" />
                </div>
              </div>
              <div className="col-lg-6 order-lg-1">
                <div className="p-5">
                  <h2 className="display-4">Vision</h2>
                  <p>
                    <li>
                      DFLCMCFI is highly recognized learning institution that
                      develops committed, hardworking, God-fearing and
                      progressive individuals possessing essential competencies
                      for life-long learning and employment in order to become
                      responsive to the needs of the 21st century.
                    </li>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section id="corevalues">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-lg-6">
                <div className="p-20">
                  <img className="img-fluid rounded-circle" src={""} alt="" />
                </div>
              </div>
              <div className="col-lg-6">
                <div className="p-5">
                  <h2 className="display-4">Core values</h2>

                  <ul>
                    <li>Maka-Diyos</li>
                    <p>
                      Expresses one's spiritual beliefs while respecting the
                      spiritual beliefs of others. Show adherence to ethical
                      principles by upholding truth.
                    </p>
                    <li>Makatao</li>
                    <p>
                      Is sensitive to individual social, and cultural
                      differences. Demonstrates contributions towards
                      solidarity.
                    </p>
                    <li>Maka-Kalikasan</li>
                    <p>
                      Cares for the environment and utilize resources
                      wisely,judiciously and economically.
                    </p>
                    <li>Makabanse</li>
                    <p>
                      Demonstrates pride in being a Filipino;exercise the rights
                      and responsibilities of a Filipino citizen. Demonstrates a
                      appropriate behavior in carrying out activities in the
                      school community and country.
                    </p>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
