import React from "react";
import { database } from "../Components/firebaseCloud";
import _ from "lodash";

class StudentAccess extends React.Component {
  constructor(props) {
    super(props);

    this.records = {};
    this.grades = {};

    this.studentID = {
      id: ""
    };
  }

  getGrades = e => {
    e.preventDefault();
    let studentID = document.getElementById("studentID").value;
    database
      .ref("students/" + studentID + "/grades")
      .once("value")
      .then(doc => {
        const data = doc.val();

        this.grades = data;

        console.log(this.grades);
        this.getRecords(studentID);
      })
      .catch(function(error) {
        alert("That record doesnt exist");
        document.getElementById("studentID").value = "";
        console.log("Error getting document:", error);
      });
  };

  getRecords = ID => {
    database
      .ref("students/" + ID)
      .once("value")
      .then(doc => {
        const data = doc.val();

        this.records = data;
        this.forceUpdate();
        console.log(this.records);
        this.fillAllinput();
      })
      .catch(function(error) {
        alert("That record doesnt exist");
        document.getElementById("studentID").value = "";
        console.log("Error getting document:", error);
      });
  };

  fillAllinput = () => {
    document.getElementById("firstname").value = this.records.firstname;
    document.getElementById("middlename").value = this.records.middlename;
    document.getElementById("surname").value = this.records.lastname;

    document.getElementById("course").value = this.records.course;
    document.getElementById("year").value = this.records.year;
    document.getElementById("semester").value = this.records.semester;
    document.getElementById("birthday").value = this.records.birthday;
    document.getElementById("semesterYear").value = this.records.semesterYear;
  };

  render() {
    let arrayKeys, arrayValues, arrayTest, a;
    if (this.grades != null) {
      arrayKeys = Object.keys(this.grades);
      arrayValues = Object.values(this.grades);
      arrayTest = [...arrayValues];
      a = -1;
    }
    return (
      <div
        className="container text anyClass bgAp centered"
        style={{
          minwidth: 500,
          maxWidth: 800
        }}
      >
        <h3>Grade Search</h3>
        <p>
          This section provides easy access in searching the grades of the
          students.
          <br /> Kindly enter your student ID. *Required
        </p>
        <div className="row">
          <div className="col">
            <img
              style={{
                marginTop: 28,
                backgroundImage: 'url("components/logo.png")'
              }}
            />
            <form
              onSubmit={this.getGrades}
              className="bgLib pb-3 pt-3 px-5 text-center"
              style={{ borderRadius: 35 }}
            >
              <h4 className="text-center">Enter your Student I.D.</h4>
              <input
                type="text"
                name="studentID"
                id="studentID"
                placeholder="Student I.D."
                className="form-control no-border text-center"
              />
              <br />
              <button className="btn btn-primary btn-sm text-center">
                Search
              </button>
              <br />
            </form>

            <hr />
            <input
              type="text"
              name="firstname"
              id="firstname"
              readOnly
              required
              placeholder="FN"
              className="form-control no-border"
            />
            <input
              type="text"
              name="middlename"
              id="middlename"
              readOnly={true}
              placeholder="MN"
              className="form-control no-border"
            />
            <input
              type="text"
              name="surname"
              id="surname"
              readOnly={true}
              placeholder="LN"
              className="form-control no-border"
            />
            <input
              type="text"
              name="birthday"
              id="birthday"
              placeholder="BD"
              readOnly={true}
              autoFocus
              className="form-control no-border"
            />
            <input
              type="text"
              name="course"
              id="course"
              readOnly
              placeholder="COURSE"
              className="form-control no-border"
            />
            <input
              type="text"
              name="year"
              id="year"
              readOnly
              placeholder="YEAR"
              className="form-control no-border"
            />
            <input
              type="text"
              name="semester"
              id="semester"
              readOnly
              placeholder="SEMESTER"
              className="form-control no-border"
            />
            <input
              type="text"
              name="semesterYear"
              id="semesterYear"
              readOnly
              placeholder="S.Y"
              className="form-control no-border"
            />
          </div>
          <div className="col pt-5  text-center">
            <table className="table table-responsive  ">
              <thead>
                <tr>
                  <th
                    style={{
                      backgroundColor: "#02709f",
                      minWidth: 150
                    }}
                  >
                    Subject
                  </th>
                  <th style={{ backgroundColor: "#02709f", minWidth: 150 }}>
                    Grade
                  </th>
                </tr>
              </thead>
              <tbody>
                {arrayKeys && // if it exist
                  arrayKeys.map(subjectGrade => {
                    if (subjectGrade !== "null") {
                      a = a + 1;
                      if (arrayTest[a] > 75) {
                        // then map it
                        return (
                          <tr key={a + "tr"}>
                            <td key={arrayKeys[a]}>
                              <input
                                defaultValue={subjectGrade}
                                id={arrayValues[a]}
                                style={{ color: "black" }}
                                disabled={true}
                              />
                            </td>
                            <td key={subjectGrade + a}>
                              <input
                                defaultValue={arrayTest[a]}
                                name={subjectGrade}
                                onChange={this.handleChange}
                                disabled={true}
                                type="number"
                              />
                            </td>
                          </tr>
                        );
                      } else if (arrayTest[a] < 75) {
                        // then map it
                        return (
                          <tr key={a + "tr"}>
                            <td
                              key={arrayKeys[a]}
                              style={{ backgroundColor: "#f88a8a" }}
                            >
                              <input
                                defaultValue={subjectGrade}
                                id={arrayValues[a]}
                                style={{ color: "black" }}
                                disabled={true}
                              />
                            </td>
                            <td
                              key={subjectGrade + a}
                              style={{ backgroundColor: "#f88a8a" }}
                            >
                              <input
                                defaultValue={arrayTest[a]}
                                name={subjectGrade}
                                onChange={this.handleChange}
                                disabled={true}
                                type="text"
                              />
                            </td>
                          </tr>
                        );
                      } else {
                        // then map it
                        return (
                          <tr key={a + "tr"}>
                            <td
                              key={arrayKeys[a]}
                              style={{ backgroundColor: "#b0b0b0" }}
                            >
                              <input
                                defaultValue={subjectGrade}
                                id={arrayValues[a]}
                                style={{ color: "black" }}
                                disabled={true}
                              />
                            </td>
                            <td
                              key={subjectGrade + a}
                              style={{ backgroundColor: "#b0b0b0" }}
                            >
                              <input
                                defaultValue={arrayTest[a]}
                                name={subjectGrade}
                                onChange={this.handleChange}
                                disabled={true}
                                type="text"
                              />
                            </td>
                          </tr>
                        );
                      }
                    }
                  })}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    );
  }
}
export default StudentAccess;
