import React, { PureComponent } from "react";
import { Link } from "react-router-dom";
import { BrowserRouter as Router, Route } from "react-router-dom";

import { auth, database, authProvider } from "../Components/firebaseCloud";

import { NavDropdown } from "react-bootstrap";
////////////////////
import SchoolAnnouncements from "../AdminAccess/SchoolAnnouncements";
import Course from "../AdminAccess/Course";
import SchoolEvents from "../AdminAccess/SchoolEvents";
import RegisterForm from "./RegisterForm";
import BsComSciControl from "../Programs/BsComSciControl";
import BsAccountControl from "../Programs/BsAccountControl";
import BsBioControl from "../Programs/BsBioControl";
import BsEnglishControl from "../Programs/BsEnglishControl";
import BsFilipinoControl from "../Programs/BsFilipinoControl";
import BsMathControl from "../Programs/BsMathControl";
import BsSsControl from "../Programs/BsSsControl";
import BsTleControl from "../Programs/BsTleControl";
import LibraryControl from "../Library/LibraryControl";
import BsBaControl from "../Programs/BsBaControl";
import CollegeControl from "../Exam/CollegeControl";
import HighSchoolControl from "../Exam/HighSchoolControl";
import BsMidWiferyControl from "../Programs/BsMidWiferyControl";
import AddData from "../StudentGrades/AddData";
import AdminEdit from "../StudentGrades/AdminEdit";
import Graduates from "../AdminAccess/Graduates";
import BsEdControl from "../Programs/BsEdControl";

function ScAnnounce() {
  return (
    <div>
      <SchoolAnnouncements />
    </div>
  );
}

function Program() {
  return (
    <div>
      <Course />
    </div>
  );
}

function ScEvent() {
  return (
    <div>
      <SchoolEvents />
    </div>
  );
}

function RForm() {
  return (
    <div>
      <RegisterForm />
    </div>
  );
}

////College contorl//////////////

function BsComSci() {
  return (
    <div>
      <BsComSciControl />
    </div>
  );
}
function BsAccount() {
  return (
    <div>
      <BsAccountControl />
    </div>
  );
}

class AdminMainPage extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {};
  }

  google() {
    console.log("I am google function");
    //Provider
    var provider = new authProvider.GoogleAuthProvider();
    var promise = auth.signInWithPopup(provider);
    //firebase.auth().signInWithRedirect(provider);
    //Good
    promise.then(result => {
      var user = result.user;
      console.log(result);
      //Upload the data to
      database.ref("users/" + user.uid).set({
        email: user.email,
        name: user.displayName
      });
    });
    //Bad
    promise.catch(e => {
      var err = e.message;
      console.log(err);
    });
  }

  render() {
    return (
      <Router  basename={process.env.PUBLIC_URL}>
        <div className="d-flex bgMainControl " id="wrapper">
          <div className="bg-light border-right" id="sidebar-wrapper">
            <div className="sidebar-heading">Main Controls</div>
            <div className="list-group list-group-flush">
              <Link
                to="/CollegeExamSchedule"
                className="list-group-item list-group-item-action bg-light"
              >
                College Exam Schedule
              </Link>
              <Link
                to="/HighExamSchedule"
                className="list-group-item list-group-item-action bg-light"
              >
                Grade to HighSchool Exam Schedule
              </Link>

              <Link
                to="/LibraryControl"
                className="list-group-item list-group-item-action bg-light"
              >
                Library
              </Link>
              <Link
                to="/scannounce"
                className="list-group-item list-group-item-action bg-light"
              >
                School Announcements
              </Link>
              <Link
                to="/scevent"
                className="list-group-item list-group-item-action bg-light"
              >
                School Events
              </Link>

              <button
                onClick={this.google}
                className="list-group-item list-group-item-action bg-light"
              >
                Sign-up Google Account
              </button>
            </div>
          </div>

          <div id="page-content-wrapper">
            <nav className="navbar navbar-expand-lg navbar-light bg-light border-bottom">
              <button className="btn btn-primary" id="menu-toggle">
                Toggle Menu
              </button>

              <div
                className="collapse navbar-collapse"
                id="navbarSupportedContent"
              >
                <ul className="navbar-nav ml-auto mt-2 mt-lg-0">
                  <li>
                    <NavDropdown title="Program Curriculum" id="">
                      <Link
                        to="/BsCsControlMainControl"
                        className="dropdown-item"
                      >
                        Bs in Computer Science
                      </Link>
                      <Link
                        to="/BsAccountControlMainControl"
                        className="dropdown-item"
                      >
                        Bs in Accountancy
                      </Link>
                      <Link
                        to="/BsBaControlMainControl"
                        className="dropdown-item"
                      >
                        Bs in Business Administration
                      </Link>
                      <Link to="/BsBioControl" className="dropdown-item">
                        Bachelor of Secondary Education Major in Biological
                        Science
                      </Link>
                      <Link to="/BsEnglishControl" className="dropdown-item">
                        Bachelor of Secondary Education Major in English
                      </Link>
                      <Link to="/BsFilipinoControl" className="dropdown-item">
                        Bachelor of Secondary Education Major in Filipino
                      </Link>
                      <Link to="/BsMathControl" className="dropdown-item">
                        Bachelor of Secondary Education Major in Mathematics
                      </Link>
                      <Link to="/BsSsControl" className="dropdown-item">
                        Bachelor of Secondary Education Major in Social Studies
                      </Link>
                      <Link to="/BsTleControl" className="dropdown-item">
                        Bachelor of Secondary Education Major in Technology and
                        Livelihood Education
                      </Link>
                      <Link to="/BsMidControl" className="dropdown-item">
                        Bachelor of Science in Midwifery
                      </Link>
                      <Link to="/BsEdControl" className="dropdown-item">
                        Bachelor of Secondary Education
                      </Link>

                      <NavDropdown.Divider />
                      <NavDropdown.Item href="#">
                        Separated link
                      </NavDropdown.Item>
                    </NavDropdown>
                  </li>
                  <li>
                    <NavDropdown title="Student Grades">
                      <Link to="/StudentInfoAdd" className="dropdown-item">
                        Add Student Record
                      </Link>
                      <Link to="/StudentInfoEdit" className="dropdown-item">
                        Add Student Grade
                      </Link>
                      <Link to="/GradControl" className="dropdown-item">
                        Graduattion Candidate Record
                      </Link>
                    </NavDropdown>
                  </li>

                  <li>
                    <NavDropdown title="Admin" id="">
                      <Link to="/registerForm" className="dropdown-item">
                        Create new User.
                      </Link>
                    </NavDropdown>
                  </li>
                </ul>
              </div>
            </nav>

            <div className="container-fluid anyClass centered">
              <Route exact path="/scannounce" component={ScAnnounce} />
              <Route exact path="/LibraryControl" component={LibraryControl} />
              <Route
                exact
                path="/CollegeExamSchedule"
                component={CollegeControl}
              />
              <Route
                exact
                path="/HighExamSchedule"
                component={HighSchoolControl}
              />

              <Route exact path="/program" component={Program} />
              <Route exact path="/scevent" component={ScEvent} />
              <Route exact path="/registerForm" component={RForm} />

              <Route
                exact
                path="/BsCsControlMainControl"
                component={BsComSci}
              />
              <Route
                exact
                path="/BsAccountControlMainControl"
                component={BsAccount}
              />
              <Route
                exact
                path="/BsBaControlMainControl"
                component={BsBaControl}
              />
              <Route exact path="/BsBioControl" component={BsBioControl} />
              <Route
                exact
                path="/BsEnglishControl"
                component={BsEnglishControl}
              />
              <Route
                exact
                path="/BsFilipinocontrol"
                component={BsFilipinoControl}
              />
              <Route exact path="/BsMathControl" component={BsMathControl} />
              <Route exact path="/BsSsControl" component={BsSsControl} />
              <Route exact path="/BsTleControl" component={BsTleControl} />
              <Route
                exact
                path="/BsMidControl"
                component={BsMidWiferyControl}
              />
              <Route exact path="/BsEdControl" component={BsEdControl} />

              <Route exact path="/StudentInfoAdd" component={AddData} />
              <Route exact path="/StudentInfoEdit" component={AdminEdit} />
              <Route exact path="/GradControl" component={Graduates} />
            </div>
          </div>
        </div>
      </Router>
    );
  }
}

export default AdminMainPage;
