import React, { Component } from "react";

import { connect } from "react-redux";
import { getExamHigh } from "../Redux/actionFunction";
import { MDBDataTable } from "mdbreact";
import _ from "lodash";

class HighSchool extends Component {
  componentDidMount() {
    this.props.getExamHigh();
  }

  /////////MDBTable

  table = () => {
    const data = {
      columns: [
        {
          label: "Year Level",
          field: "yearlvl",
          sort: "asc",
          width: 150
        },
        {
          label: "Section",
          field: "section",
          sort: "asc",
          width: 150
        },
        {
          label: "Subject",
          field: "subject",
          sort: "asc",
          width: 270
        },
        {
          label: "Exam Type",
          field: "examtype",
          sort: "asc",
          width: 270
        },
        {
          label: "Notes",
          field: "notes",
          sort: "asc",
          width: 200
        },
        {
          label: "Date",
          field: "date",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        yearlvl: record.yearlvl,
        section: record.section,
        subject: record.subject,
        examtype: record.examtype,
        notes: record.notes,
        date: record.date
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };

  render() {
    return (
      <div className="container-fluid pt-5 anyClass centered bgHigh">
        <div className="container-fluid">
          <h3>Grade School to High School Exam Schedule</h3>
          <hr />
          <p className="lead">
            This section provides an overview of exam scheduling
          </p>
          <br />

          <div>
            <section className="container-fluid">{this.table()}</section>
          </div>
        </div>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getExamHigh
})(HighSchool);
