import React, { Component } from "react";
import { database, auth } from "../Components/firebaseCloud";

export default class RegisterForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      email: "",
      password: "",
      passwordRe: "",
      name: "",
      surname: "",
      position: ""
    };
    this.onChange = this.onChange.bind(this);
  }

  onChange(e) {
    this.setState({
      [e.target.name]: e.target.value
    });
    console.log(this.state);
  }

  //Sign up function
  signUp(event) {
    event.preventDefault();
    const email = this.refs.email.value;
    const password = this.refs.password.value;

    //firebase authentication
    if (this.state.password === this.state.passwordRe) {
      //save the data in database
      const promise = auth.createUserWithEmailAndPassword(email, password);
      //Promise Good
      promise.then(user => {
        alert("Account Created " + email);

        database.ref("users/" + user.uid).set({
          email: email
          // name: this.state.name,
          // surname: this.state.surname,
          // position: this.state.position
        });

        this.props.history.push("/AdminMainControl");
      });
      //Promise Bas
      promise.catch(e => {
        alert(e.message);
      });
    } else {
      alert("Password not match");
    }
  }

  render() {
    return (
      <div className="registerForm">
        <h3 className="m-3 d-flex justify-content-center">Create new user</h3>

        <div className="m-3 d-flex justify-content-center">
          <div className="form-group"></div>
          <form onSubmit={this.signUp.bind(this)}>
            <div className="container-fluid">
              <input
                id="email"
                name="email"
                ref="email"
                type="email"
                required
                onChange={e => {
                  this.onChange(e);
                }}
                placeholder="Enter your email"
                className="logInInput form-control mx-4 d-flex justify-content-center"
              />
              <br />
              <input
                id="pass"
                name="password"
                ref="password"
                type="password"
                required
                onChange={e => {
                  this.onChange(e);
                }}
                placeholder="Enter your password"
                className=" logInInput mx-4 form-control d-flex"
              />
              <br />
              <input
                id="passRe"
                name="passwordRe"
                ref="passwordRe"
                type="password"
                required
                onChange={e => {
                  this.onChange(e);
                }}
                placeholder="Re-Enter your password"
                className=" logInInput mx-4 form-control d-flex"
              />
              <br />

              <br />
              <button
                id="sumbit"
                className="btn btn-success  mx-4 col-sm-10"
                type="submit"
              >
                Submit
              </button>
            </div>
          </form>
        </div>
      </div>
    );
  }
}
