import React, { Component } from "react";

import { database } from "../Components/firebaseCloud";

class AddData extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      studentID: "",
      firstname: "",
      middlename: "",
      lastname: "",
      currentSemester: "",
      semesterYear: "",
      birthday: "",
      yearLevel: "",
      course: ""
    };
  }

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
    console.log(this.state);
  };

  handleSubmit = e => {
    e.preventDefault();
    const {
      firstname,
      middlename,
      lastname,
      studentID,
      currentSemester,
      yearLevel,
      course,
      birthday,
      semesterYear
    } = this.state;

    database
      .ref("students/" + studentID)
      .once("value")
      .then(function(doc) {
        const data = doc.val();
        if (data !== null) {
          alert("Student ID already Exist");
        } else {
          // doc.data() will be undefined in this case

          database.ref("students/" + studentID).set({
            firstname: firstname,
            middlename: middlename,
            lastname: lastname,
            birthday: birthday,
            course: course,
            year: yearLevel,
            semester: currentSemester,
            semesterYear: semesterYear
          });
          database
            .ref("students/" + studentID + "/grades")
            .set({
              null: "null"
            })
            .catch(function(error) {
              alert("fuuuuuuck!");
              console.log("Error getting document:", error);
            });

          alert("Student successfully created");
          //window.location.reload();
        }
      })
      .catch(function(error) {
        alert("fuuuuuuck!");
        console.log("Error getting document:", error);
      });
  };

  render() {
    return (
      <div>
        <div
          className="container-fluid text-center"
          style={{
            minwidth: 500,
            maxWidth: 1000
          }}
        >
          <h3>Add Student Record</h3>
          <form onSubmit={this.handleSubmit}>
            <div className="form-group">
              <input
                onChange={this.handleChange}
                value={this.state.id}
                type="text"
                className="form-control no-border "
                placeholder="ID"
                required
                autoComplete="off"
                id="studentID"
                name="studentID"
              />
            </div>

            <div className="input-group mb-3">
              <div className="input-group-prepend">
                <span
                  className="input-group-text"
                  id="basic-addon1"
                  style={{ width: 150 }}
                >
                  Name
                </span>
              </div>
              <input
                type="text"
                className="form-control text-center"
                placeholder="e.g. Joe"
                aria-describedby="basic-addon1"
                name="firstname"
                id="firstname"
                autoComplete="off"
                onChange={this.handleChange}
                required
              />
            </div>

            <div className="input-group mb-3">
              <div className="input-group-prepend">
                <span
                  className="input-group-text"
                  id="basic-addon1"
                  style={{ width: 150 }}
                >
                  Middle Name
                </span>
              </div>
              <input
                type="text"
                className="form-control text-center"
                placeholder="e.g. Joe"
                aria-describedby="basic-addon1"
                name="middlename"
                id="middlename"
                autoComplete="off"
                onChange={this.handleChange}
                required
              />
            </div>

            <div className="input-group mb-3">
              <div className="input-group-prepend">
                <span
                  className="input-group-text"
                  id="basic-addon1"
                  style={{ width: 150 }}
                >
                  Surname
                </span>
              </div>
              <input
                type="text"
                className="form-control text-center"
                placeholder="e.g. Joe"
                aria-describedby="basic-addon1"
                name="lastname"
                id="lastname"
                autoComplete="off"
                onChange={this.handleChange}
                required
              />
            </div>

            <div className="input-group mb-3">
              <div className="input-group-prepend">
                <span
                  className="input-group-text"
                  id="basic-addon1"
                  style={{ width: 150 }}
                >
                  BirthDay
                </span>
              </div>
              <input
                type="date"
                id="birthday"
                name="birthday"
                className="shortFieldWidth browser-default text-center"
                defaultValue="1900-01-01"
                autoComplete="off"
                onChange={this.handleChange}
                required
              />
            </div>

            <div className="input-group mb-3">
              <div className="input-group-prepend">
                <span
                  className="input-group-text"
                  id="basic-addon1"
                  style={{ width: 150 }}
                >
                  Course
                </span>
              </div>
              <input
                id="course"
                name="course"
                onChange={this.handleChange}
                style={{ width: 150 }}
                list="courseList"
                autoComplete="off"
                placeholder="course"
                onClick="selectText()"
                required
                className="shortFieldWidth browser-default text-center"
              />
              <datalist id="courseList">
                <option value="">Select...</option>
                <option value="BSCS" />
                <option value="BSBA" />
                <option value="BSED" />
                <option value="BSN" />
              </datalist>

              <div className="input-group-prepend">
                <span
                  className="input-group-text"
                  id="basic-addon1"
                  style={{ width: 150 }}
                >
                  Year Level
                </span>
              </div>
              <input
                id="yearLevel"
                name="yearLevel"
                className="shortFieldWidth browser-default text-center"
                style={{ width: 150 }}
                list="yearlevels"
                placeholder="year"
                autoComplete="off"
                onChange={this.handleChange}
                required
              />
              <datalist id="yearlevels">
                <option value="">Select...</option>
                <option value="1st" />
                <option value="2nd" />
                <option value="3rd" />
                <option value="4th" />
                <option value="Masteral" />
              </datalist>

              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span
                    className="input-group-text"
                    id="basic-addon1"
                    style={{ width: 150 }}
                  >
                    Semester
                  </span>
                </div>
                <input
                  id="currentSemester"
                  name="currentSemester"
                  className="shortFieldWidth browser-default text-center"
                  style={{ width: 150 }}
                  list="currentSemesters"
                  placeholder="current sem"
                  autoComplete="off"
                  onChange={this.handleChange}
                  required
                />
                <datalist id="currentSemesters">
                  <option value="">Select...</option>
                  <option value="1st" />
                  <option value="2nd" />
                  <option value="Summer" />
                </datalist>
              </div>

              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span
                    className="input-group-text"
                    id="basic-addon1"
                    style={{ width: 150 }}
                  >
                    School Year
                  </span>
                </div>
                <input
                  className="shortFieldWidth browser-default text-center"
                  onChange={this.handleChange}
                  autoComplete="off"
                  type="number"
                  id="semesterYear"
                  name="semesterYear"
                  min="1975"
                  max="3000"
                  placeholder={new Date().getFullYear()}
                  onChange={this.handleChange}
                  required
                ></input>
              </div>
            </div>

            <button className="btn btn-primary">Submit Record</button>
          </form>
        </div>
      </div>
    );
  }
}

export default AddData;
