import React, { Component } from "react";

import { auth } from "../Components/firebaseCloud";

import Auth from "../Route/Auth";
//Icons
import { IoIosKey } from "react-icons/io";

//important connect with Main Router Link
import { withRouter } from "react-router-dom";

class LoginPage extends Component {
  //Functions
  logIn() {
    const email = this.refs.email.value;
    const password = this.refs.password.value;

    //search the data to log in
    const promise = auth.signInWithEmailAndPassword(email, password);

    //TODO: handle login promise
    promise.then(response => {
      Auth.authenticate();
      //restore the logout button

      var lout = document.getElementById("logout");
      lout.classList.remove("hide");

      var lout1 = document.getElementById("adpage");
      lout1.classList.remove("hide");

      var lout2 = document.getElementById("adaccess");
      lout2.classList.add("hide");

      alert("Welcome " + email);
      //redirect

      this.props.handleSuccessfulAuth(response);
    });

    //catch the error
    promise.catch(e => {
      alert(e.message);
      var err = e.message;

      this.setState({ err: err });
    });
  }

  logOut() {
    //fireabse authentication Signout
    this.props.handleLogout();
  }

  constructor(props) {
    super(props);

    this.state = {
      //state for message
      err: "",

      user: null
    };

    //bind the function
    this.logIn = this.logIn.bind(this);
    // this.google = this.google.bind(this);
  }

  render() {
    return (
      //inputs
      <div className="loginFont logIn " id="loginheader">
        <div className="form">
          <h3 className="loginFont ">Admin access Only</h3>
          <h5>Status: {this.props.Status}</h5>
          <input
            id="email"
            ref="email"
            type="email"
            placeholder="Enter your email"
            className="logInInput form-control mx-4 d-flex justify-content-center"
          />{" "}
          <br />
          <input
            id="pass"
            ref="password"
            type="password"
            placeholder="Enter your password"
            className=" logInInput mx-4 form-control d-flex"
          />{" "}
          <br />
          <button onClick={this.logIn} className="btn btn-success ">
            Log in <IoIosKey />
          </button>
          <br />
          <p>This section is for administrator only.</p>
        </div>
      </div>
    );
  }
}
export default withRouter(LoginPage);
