import React, { Component } from "react";

export default class VisionModal extends Component {
  render() {
    return (
      <div>
        <div
          className="modal fade"
          id="visionModal"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="exampleModalCenterTitle"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-dialog-centered" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLongTitle">
                  Vision
                </h5>

                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-label="Close"
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <div>
                  <p>
                    DFLCMCFI is highly recognized learning institution that
                    develops committed, hardworking, God-fearing and progressive
                    individuals possessing essential competencies for life-long
                    learning and employment in order to become responsive to the
                    needs of the 21st century.
                  </p>
                </div>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-secondary"
                  data-dismiss="modal"
                >
                  Close
                </button>
                {/* <button type="button" className="btn btn-primary">
            Save changes
          </button> */}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
