import React, { Component } from "react";

import { connect } from "react-redux";
import { getExamCol, deleteExamCol } from "../Redux/actionFunction";
import { dbExamCol } from "../Components/firebaseCloud";
import { MDBDataTable, MDBBtn } from "mdbreact";
import _ from "lodash";
//Icons
import { MdDeleteForever } from "react-icons/md";
import { IoIosSave } from "react-icons/io";

class CollegeControl extends Component {
  constructor(props) {
    super(props);

    this.state = {
      department: "",
      subject: "",
      examtype: "",
      notes: "",
      date: ""
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.getExamCol();
  }
  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  handleSubmit(e) {
    e.preventDefault();

    dbExamCol.push({
      department: this.state.department,
      subject: this.state.subject,
      examtype: this.state.examtype,
      notes: this.state.notes,
      date: this.state.date
    });
    //reset field for Selection
    this.setState({
      department: "",
      subject: "",
      notes: ""
      // date: ""
    });
    this.examtype.value = this.state.examtype;
  }

  /////////MDBTable

  table = () => {
    const data = {
      columns: [
        {
          label: "Department",
          field: "department",
          sort: "asc",
          width: 150
        },
        {
          label: "Subject",
          field: "subject",
          sort: "asc",
          width: 270
        },
        {
          label: "Exam Type",
          field: "examtype",
          sort: "asc",
          width: 270
        },
        {
          label: "Notes",
          field: "notes",
          sort: "asc",
          width: 200
        },
        {
          label: "Date",
          field: "date",
          sort: "asc",
          width: 200
        },
        {
          label: "Button",
          field: "handle",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        department: record.department,
        subject: record.subject,
        examtype: record.examtype,
        notes: record.notes,
        date: record.date,

        handle: (
          <MDBBtn
            className="btn btn-danger"
            onClick={() => this.props.deleteExamCol(key)}
            size="sm"
          >
            <MdDeleteForever />
          </MDBBtn>
        )
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };

  render() {
    return (
      <div>
        {" "}
        <header className="masthead text-center text-white" id="accheader">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0">College Exam Scheduler</h1>
            </div>
          </div>
          <div className="bg-circle-1 bg-circle"></div>
          <div className="bg-circle-2 bg-circle"></div>
          <div className="bg-circle-3 bg-circle"></div>
          <div className="bg-circle-4 bg-circle"></div>
        </header>
        <section id="booksection" className="container-fluid">
          <form
            onSubmit={this.handleSubmit}
            className="m-3 d-flex justify-content-center"
          >
            <div>
              <input
                onChange={this.handleChange}
                value={this.state.department}
                type="text"
                name="department"
                className="form-control col-sm-12"
                placeholder="Department"
                required
              />
              <input
                onChange={this.handleChange}
                value={this.state.subject}
                type="text"
                name="subject"
                className="form-control no-border"
                placeholder="Subject"
                required
              />
              <select
                id="examtype"
                className="form-control no-border"
                name="examtype"
                onChange={this.handleChange}
                required
                defaultValue={this.state.examtype}
                ref={ref => (this.examtype = ref)}
              >
                <option value={this.state.examtype} disabled>
                  Please select exam type
                </option>

                <option
                  name="examtype"
                  value="Prelims"
                  onChange={this.handleChange}
                >
                  Prelims
                </option>
                <option
                  name="examtype"
                  value="Midterm"
                  onChange={this.handleChange}
                >
                  Midterm
                </option>
                <option
                  name="examtype"
                  value="Pre-Finals"
                  onChange={this.handleChange}
                >
                  Pre-Finals
                </option>
                <option
                  name="examtype"
                  value="Finals"
                  onChange={this.handleChange}
                >
                  Finals
                </option>
              </select>
              <input
                onChange={this.handleChange}
                value={this.state.notes}
                type="text"
                name="notes"
                className="form-control no-border"
                placeholder="Additional Notes"
              />
              <input
                onChange={this.handleChange}
                value={this.state.date}
                type="date"
                name="date"
                className="form-control no-border"
                placeholder="Date"
                required
              />

              <button type="submit" className="btn btn-secondary col-sm-12">
                <IoIosSave />
              </button>
            </div>
          </form>
        </section>
        <section className="text-center">{this.table()}</section>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getExamCol,
  deleteExamCol
})(CollegeControl);
