import React, { Component } from "react";
import { connect } from "react-redux";
import { getBsEnglish, deleteBsEnglish } from "../Redux/actionFunction";
import { dbBsEnglish } from "../Components/firebaseCloud";
import { MDBDataTable, MDBBtn } from "mdbreact";
import _ from "lodash";

class BsEnglishControl extends Component {
  constructor(props) {
    super(props);

    this.state = {
      subject: "",
      unit: "",
      prerequisites: "",
      sem: "",
      year: ""
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.getBsEnglish();
  }
  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  handleSubmit(e) {
    e.preventDefault();

    dbBsEnglish.push({
      unit: this.state.unit,
      subject: this.state.subject,
      prerequisites: this.state.prerequisites,
      sem: this.state.sem,
      year: this.state.year
    });
    //Reset field for inputs
    this.setState({
      subject: "",
      unit: "",
      prerequisites: "",
      sem: "",
      year: ""
    });
    //reset field for Selection
    this.unit.value = this.state.unit;
    this.sem.value = this.state.sem;
    this.year.value = this.state.year;
  }

  /////////MDBTable

  table = () => {
    const data = {
      columns: [
        {
          label: "Subject",
          field: "subject",
          sort: "asc",
          width: 150
        },
        {
          label: "Unit",
          field: "unit",
          sort: "asc",
          width: 270
        },
        {
          label: "Pre-Requisites",
          field: "prerequisites",
          sort: "asc",
          width: 200
        },
        {
          label: "Semester",
          field: "semester",
          sort: "asc",
          width: 200
        },
        {
          label: "Year",
          field: "year",
          sort: "asc",
          width: 200
        },
        {
          label: "Button",
          field: "handle",
          sort: "asc",
          width: 200
        }
      ],
      rows: []
    };

    _.map(this.props.records, (record, key) => {
      const row = {
        subject: record.subject,
        unit: record.unit,
        prerequisites: record.prerequisites,
        semester: record.sem,
        year: record.year,
        handle: (
          <MDBBtn
            className="btn btn-danger"
            onClick={() => this.props.deleteBsEnglish(key)}
            size="sm"
          >
            Delete
          </MDBBtn>
        )
      };

      return data.rows.push(row);
    });

    return <MDBDataTable bordered striped data={data} />;
  };

  render() {
    return (
      <div>
        <header className="masthead text-center text-white">
          <div className="masthead-content">
            <div className="container">
              <h1 className="masthead-heading mb-0">
                Bachelor of Secondary Education Major in English
              </h1>
              <p>
                In education, a curriculum is broadly defined as the totality of
                student experiences that occur in the educational process. The
                term often refers specifically to a planned sequence of
                instruction, or to a view of the student's experiences in terms
                of the educator's or school's instructional goals.
              </p>
            </div>
          </div>
        </header>
        <div className="container-fluid">
          <section id="curriculum">
            <div className="container">
              <div className="row align-items-center">
                <div className="col-lg-6">
                  <div className="p-5">
                    <div className="table-wrapper-scroll-y my-custom-scrollbar"></div>
                  </div>
                </div>
                <div className="col-lg-5">
                  <div className="p-5">
                    <h2 className="display-5">Program Curriculum Editor</h2>
                    <form onSubmit={this.handleSubmit}>
                      <div>
                        <input
                          onChange={this.handleChange}
                          value={this.state.subject}
                          type="text"
                          name="subject"
                          className="form-control no-border"
                          placeholder="Subject"
                          required
                        />
                        <select
                          id="unit"
                          className="form-control no-border"
                          name="unit"
                          onChange={this.handleChange}
                          required
                          defaultValue={this.state.unit}
                          ref={ref => (this.unit = ref)}
                        >
                          <option value={this.state.unit} disabled>
                            Please select how many Units
                          </option>

                          <option
                            name="unit"
                            value="1"
                            onChange={this.handleChange}
                          >
                            1
                          </option>
                          <option
                            name="unit"
                            value="2"
                            onChange={this.handleChange}
                          >
                            2
                          </option>
                          <option
                            name="unit"
                            value="3"
                            onChange={this.handleChange}
                          >
                            3
                          </option>
                          <option
                            name="unit"
                            value="4"
                            onChange={this.handleChange}
                          >
                            4
                          </option>
                          <option
                            name="unit"
                            value="5"
                            onChange={this.handleChange}
                          >
                            5
                          </option>
                          <option
                            name="unit"
                            value="6"
                            onChange={this.handleChange}
                          >
                            6
                          </option>
                        </select>
                        <input
                          onChange={this.handleChange}
                          value={this.state.prerequisites}
                          type="text"
                          name="prerequisites"
                          className="form-control no-border"
                          placeholder="Prerequisites"
                        />
                        <select
                          id="sem"
                          className="form-control no-border"
                          name="sem"
                          onChange={this.handleChange}
                          required
                          defaultValue={this.state.sem}
                          //reference for reset field
                          ref={ref => (this.sem = ref)}
                        >
                          <option value={this.state.sem} disabled>
                            Please select Semester
                          </option>
                          <option
                            name="sem"
                            value="First Sem"
                            onChange={this.handleChange}
                          >
                            First Semester
                          </option>
                          <option
                            name="sem"
                            value="Second Sem"
                            onChange={this.handleChange}
                          >
                            Second Semester
                          </option>
                        </select>

                        <select
                          id="year"
                          className="form-control no-border"
                          name="year"
                          onChange={this.handleChange}
                          required
                          defaultValue={this.state.year}
                          //reference for reset field
                          ref={ref => (this.year = ref)}
                        >
                          <option value={this.state.year} disabled>
                            Please select Year
                          </option>
                          <option value="1st Year">1st Year</option>
                          <option value="2nd Year">2nd Year</option>
                          <option value="3rd Year">3rd Year</option>
                          <option value="4th Year">4th Year</option>
                        </select>

                        <button
                          type="submit"
                          className="btn btn-secondary d-flex col-sm-12"
                        >
                          Save
                        </button>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>

        <section className="text-center">{this.table()}</section>
      </div>
    );
  }
}
//redux-connection
function mapStateToProps(state, ownProps) {
  return {
    //set the state from reducers index.js
    records: state.records
  };
}

//Pass masStatetoProps, and funtions to be used
export default connect(mapStateToProps, {
  getBsEnglish,
  deleteBsEnglish
})(BsEnglishControl);
