import React, { Component } from "react";
import "./css/App.css";
import "./css/modern-business.css";
import "./css/simple-sidebar.css";
import "./css/Header.css";

//==============================================================Page
import RouterMain from "./Route/RouterMain";

export default class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      //user State
      loggedInStatus: "No user",
      user: {},
      //Home State
      desc: "",
      picUrl: null
    };
    this.handleLogin = this.handleLogin.bind(this);
    this.handLogout = this.handLogout.bind(this);
  }

  //Login Function GM
  handleLogin(data) {
    this.setState({
      loggedInStatus: "Logged in",
      user: data.user
    });
  }
  handLogout() {
    this.setState({
      loggedInStatus: "No user",
      user: {}
    });
  }

  render() {
    return (
      <div className="full-height">
        <RouterMain
          loggedInStatus={this.state.loggedInStatus}
          handleLogin={this.handleLogin}
          handleLogout={this.handLogout}
        />
      </div>
    );
  }
}
